import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AgmCoreModule } from '@agm/core';
import { BrowserModule } from '@angular/platform-browser';

import { SharedModule } from '../../../../../../core/modules/shared.module';

import { EditFloorComponent } from './edit-floor.component';
import { FloorMapModule } from '../../../../components/floor-map/floor-map.module';

import { AuthGuard } from '../../../../../services/auth-guard-service/auth-guard.service';

const routes = [
    {
        path: 'admin/floor/edit/:id',
        component: EditFloorComponent,
        canActivate: [AuthGuard]
    }
];

@NgModule({
    declarations: [
        EditFloorComponent
    ],
    imports     : [
        SharedModule,
        RouterModule.forChild(routes),
        BrowserModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyCRozu0G6ZU2eZtpj20AheDFk-_ewFW8Ps'
        }),
        FloorMapModule
    ],
    exports     : [
        EditFloorComponent
    ],
    bootstrap: [ EditFloorComponent ]
})

export class EditFloorModule
{
}



