import { Component,Inject } from '@angular/core';
import { Router } from '@angular/router';
import { geoJSON, Layer,latLng, tileLayer,imageOverlay, circle,polygon,rectangle,CRS,LatLngBounds,Map,marker,icon, layerGroup } from 'leaflet';
import { FuseTranslationLoaderService } from '../../../../../../core/services/translation-loader.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';

@Component({
    selector   : 'add-floor',
    templateUrl: './add-floor.component.html',
    styleUrls  : ['./add-floor.component.scss']
})
export class AddFloorComponent
{
/*
  markers: Layer[] = [];

  addMarker() {
    const newMarker = marker(
        [ 46.879966 + 0.1 * (Math.random() - 0.5), -121.726909 + 0.1 * (Math.random() - 0.5) ],
        {
            icon: icon({
                iconSize: [ 25, 41 ],
                iconAnchor: [ 13, 41 ],
                iconUrl: '2273e3d8ad9264b7daa5bdbf8e6b47f8.png',
                shadowUrl: '44a526eed258222515aa21eaffd14a96.png'
            })
        }
    );

    this.markers.push(newMarker);
}
*/
chairsLayerGroup =  layerGroup();
/*[
    circle([ -150, 150 ], { radius: 2.5, fillColor:"#5E8C29",color:"#5E8C29" }),
    circle([ -150, 50 ], { radius: 2.5, fillColor:"#5E8C29",color:"#5E8C29" })
])*/
roomsLayerGroup =  layerGroup(
    // polygon([[ -79, 89 ], [ -82, 144 ], [ -109.5, 90.5 ], [-108.5, 145.5 ]])
    // new rectangle([[-83.5, 90], [-108.5, 144.5]], {color: "#ff7800", weight: 1})
);

layers = [
    this.roomsLayerGroup,
    this.chairsLayerGroup
    
    // marker([ 46.879966, -121.726909 ], {
    //     icon: icon({
    //        iconSize: [ 25, 41 ],
    //        iconAnchor: [ 13, 41 ],
    //        iconUrl: 'assets/marker-icon.png',
    //        shadowUrl: 'assets/marker-shadow.png'
    //     })})
    
    // circle([ 0, 150 ], { radius: 50 }),
    // polygon([[ 46.8, -121.85 ], [ 46.92, -121.92 ], [ 46.87, -121.8 ]]),
    // marker([0, 0 ])
];

  imageBounds = [[0,0], [1000,1000]];
    options = {
        layers: [
            // tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', { maxZoom: 18, attribution: '...' })
            // tileLayer('http://18.195.126.0/img/floors/floor_65.jpg', { maxZoom: 18, maxBounds:this.bounds,attribution: '...' })
            // tileLayer('http://18.195.126.0/img/floors/floor_65.jpg', { maxZoom: 18,attribution: '...' })
            // imageOverlay('http://18.195.126.0/img/floors/floor_65.jpg')
        ],
        zoom: 1,
        minZoom: 1,
        maxZoom: 3,
        //center: latLng(46.879966, -121.726909),
        center: latLng(0, 0),
        attributionControl: false,
        scrollWheelZoom: true,
        crs: CRS.Simple,
        zoomControl: false,
        dragging: true
       // maxBounds: this.imageBounds
    };

    layersControl = {
        overlays: {
            'Chairs': this.chairsLayerGroup,
            //'Big Circle': circle([ -150, 150 ], { radius: 50 }),
            'Rooms': this.roomsLayerGroup
        }
    }

    initFloorChairs(){
        this.chairsLayerGroup.clearLayers();
        this.chairsLayerGroup.addLayer(circle([ -150, 150 ], { radius: 2.5, fillColor:"#5E8C29",color:"#5E8C29" }));
        this.chairsLayerGroup.addLayer(circle([ -150, 200 ], { radius: 2.5, fillColor:"#5E8C29",color:"#5E8C29" }));
        this.chairsLayerGroup.addLayer(circle([ -50, 250 ], { radius: 2.5, fillColor:"#5E8C29",color:"#5E8C29" }));
    }

    onMapClick(e){
        
        var lat = e.latlng.lat;
		var lng = e.latlng.lng;
        console.log("onMapClick.lat=%o,lng=%o",lat,lng)
        this.chairsLayerGroup.addLayer(circle([ lat, lng ], { radius: 2.5, fillColor:"#5E8C29",color:"#5E8C29" }));
    }

    onMapReady(map: Map) {
        
        // console.log("onMapReady. map=",map)
        // Do stuff with map
        const url = "http://18.195.126.0/img/floors/floor_68.jpg";
        const w = 2882;
        const h = 2367;
        const southWest = map.unproject([0, h], map.getMaxZoom()-0);
        const northEast = map.unproject([w, 0], map.getMaxZoom()-0);
        const bounds = new LatLngBounds(southWest, northEast);
        const layerImage = imageOverlay(url, bounds);
        layerImage.addTo(map);
        map.setMaxBounds(bounds);
        this.initFloorChairs();
        // map.on('click', evt => this.onMapClick(evt.target.getBounds()));
        map.on('click', evt => this.onMapClick(evt));
        // map.on('click', this.onMapClick);
        // this.options["layers"].push(layerImage);
        // this.options["layers"] = [...this.options["layers"]]
       // this.options = [..this.options];
       // this.addMarker(); // test!!
       // this.chairsLayerGroup.addLayer(circle([ -150, 200 ], { radius: 2.5, fillColor:"#5E8C29",color:"#5E8C29" }));// test!!
       
     }



}




