import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable()
export class AuthService {

  baseURL: string;

  // Events
  public userAuthenticated = new EventEmitter<object>();

  ruotToEmp() {
    this.router.navigate(['/floor-plan/place-employee']);
  }
  login(email: string, password: string){
    const formData: FormData = new FormData();


    // formData.append('username', username);
    formData.append('email', email); // test!!!
    formData.append('password', password);
    formData.append('login', 'true');
   // formData.append('name', 'tehila');

    const url = this.baseURL + '/models/login.php';
    return this.httpClient.post<LoginResponse>(url, formData);
    
    // this.httpClient.post(url, formData).subscribe(
    //   (response: LoginResponse) => {
    //     console.log('response = ', response);
    //     if (response){
    //       if (response.isSucceeded && response.token) {
    //         // this.token = response.token; 
    //         this.storeToken(response.token);
    //         this.userAuthenticated.emit({ userDetails: response.userDetails});
    //         this.router.navigate(['/floor-plan/place-employee']);
    //       } else {
    //         alert('Incorrect email or password. Please try again');
    //       }
    //     } else {
    //       alert('Login failed. Please try again 1 ');
    //     }


    //   },
    //   (error) => {
    //     console.log('error = ', error);
    //     alert('Login failed. Please try again 2 ');
    //   }
    // );
  }


  logout() {
    // this.token = null;
    // this.storeToken(null);
    this.clearToken();
    this.router.navigate(['/auth/login']);
  }

  storeToken(token: string){
    // store jwt token in local storage to keep user logged in between page refreshes
    localStorage.setItem('currentUser', JSON.stringify({ token: token }));
  }

  getToken(){
    let token = null;
    const currentUser = JSON.parse(localStorage.getItem('currentUser'));
    if (currentUser){
      token = currentUser.token;
    }
    return token;
  }

  clearToken(){
    // clear token remove user from local storage to log user out
    localStorage.removeItem('currentUser');
  }

  isAuthenticated(){
    const token = this.getToken();
    return token != null;
  }

  constructor(private httpClient: HttpClient, private router: Router) {
    this.baseURL =       '//35.231.189.174'; //  '//' + window.location.hostname;
  }
   

}

interface LoginResponse { 
  isSucceeded: boolean;
  token: string;
  userDetails: object;
  // userDetailes: object;
}




