import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { FuseSplashScreenService } from './core/services/splash-screen.service';
import { TranslateService } from '@ngx-translate/core';
import { FuseTranslationLoaderService } from './core/services/translation-loader.service';

import { FuseNavigationService } from './core/components/navigation/navigation.service';
import { FuseNavigationModel } from './navigation/navigation.model';
import { locale as navigationEnglish } from './navigation/i18n/en';
import { locale as navigationTurkish } from './navigation/i18n/tr';

import { AuthService } from './main/services/auth-service/auth.service';
import { CompaniesService } from './main/services/companies-service/companies-service.service';

@Component({
    selector   : 'fuse-root',
    templateUrl: './app.component.html',
    styleUrls  : ['./app.component.scss']
})
export class AppComponent
{
    constructor(
        private router: Router,
        private fuseNavigationService: FuseNavigationService,
        private fuseSplashScreen: FuseSplashScreenService,
        private translate: TranslateService,
        private translationLoader: FuseTranslationLoaderService,
        private authService: AuthService,
        private companiesService: CompaniesService
    )
    {
        // Add languages
        this.translate.addLangs(['en', 'tr']);

        // Set the default language
        this.translate.setDefaultLang('en');

        // Use a language
        this.translate.use('en');

        // Set the navigation model
        this.fuseNavigationService.setNavigationModel(new FuseNavigationModel());   

        // Set the navigation translations
        this.translationLoader.loadTranslations(navigationEnglish, navigationTurkish);

        // Build admin menu
        this.buildAdminMenu();

        //  Update menu's companies
        if (authService.isAuthenticated()){
            this.updateCompaniesMenu();
        } else {
            this.router.navigate(['/auth/login']);
        }

        // Update companies in menu after login
        this.authService.userAuthenticated
            .subscribe(
                (event) => {
                    this.updateCompaniesMenu();
                }
            );


    }

    buildAdminMenu() {
        const navModel = this.fuseNavigationService.getNavigationModel();
        let adminPanel: any;
        adminPanel = {
            'id': 'admin-panel',
            'title': 'Admin Panel',
            'type': 'group',
            'children': [{
                'id': 'addCompany',
                'title': 'Add Company',
                'icon': 'add',
                'type': 'item',
                'url': '/admin/add-company'
            }]
        };
        navModel.push(adminPanel);
    }

    updateCompaniesMenu(){
        const navModel = this.fuseNavigationService.getNavigationModel();
        console.log('navModel = ', navModel);
        const adminPanel = navModel.find(o => o.id === 'admin-panel');
        console.log('adminPanel = ', adminPanel);
        if (adminPanel) {
            adminPanel.children = [{
                'id': 'addCompany',
                'title': 'Add Company',
                'icon': 'add',
                'type': 'item',
                'url': '/admin/add-company'
            }];
            this.companiesService.getUserCompanies()
                .subscribe(
                    (companies) => {
                        if (companies) {
                            console.log('companies = ', companies);
                            if (companies.length === 0) {
                                this.router.navigate(['/admin/add-company']);
                            } else {
                                companies.forEach(company => {
                                    const companyID = company.id;
                                    const companyName = company.name;
                                    if (companyID && companyName) {
                                        adminPanel.children.push(
                                            {
                                                'id': 'company' + companyID,
                                                'title': companyName,
                                                'type': 'collapse',
                                                'icon': 'border_all',
                                                'children': [
                                                    {
                                                        'id': 'company' + companyID + 'Organization',
                                                        'title': 'Organization',
                                                        'type': 'collapse',
                                                        'icon': 'group',
                                                        'children': [
                                                            {
                                                                'id': 'company' + companyID + 'Departments',
                                                                'title': 'Departments',
                                                                'type': 'item',
                                                                'url': '/admin/company/' + companyID + '/departments'
                                                            },
                                                            {
                                                                'id': 'company' + companyID + 'Groups',
                                                                'title': 'Groups',
                                                                'type': 'item',
                                                                'url': '/admin/company/' + companyID + '/groups'
                                                            },
                                                            {
                                                                'id': 'company' + companyID + 'Teams',
                                                                'title': 'Teams',
                                                                'type': 'item',
                                                                'url': '/admin/company/' + companyID + '/teams'
                                                            },
                                                            {
                                                                'id': 'company' + companyID + 'Employees',
                                                                'title': 'Employees',
                                                                'type': 'item',
                                                                'url': '/admin/company/' + companyID + '/employees'
                                                            }
                                                        ]
                                                    },
                                                    {
                                                        'id': 'company' + companyID + 'Architecture',
                                                        'title': 'Architecture',
                                                        'type': 'collapse',
                                                        'icon': 'location_city',
                                                        'children': [
                                                            {
                                                                'id': 'company' + companyID + 'Buildings',
                                                                'title': 'Buildings',
                                                                'type': 'item',
                                                                'url': '/admin/company/' + companyID + '/buildings'
                                                            },
                                                            {
                                                                'id': 'company' + companyID + 'Floors',
                                                                'title': 'Floors',
                                                                'type': 'item',
                                                                'url': '/admin/company/' + companyID + '/floors'
                                                            }
                                                        ]
                                                    }
                                                ]
                                            }
                                        );
                                    }

                                });
                            }

                        }
                    },
                    (error) => {
                        console.log('error = ', error);
                        navModel.push(adminPanel);
                    });
        }
    }


}



