import { Component, Inject } from '@angular/core';
import { FuseConfigService } from '../../../../../core/services/config.service';
import { FuseTranslationLoaderService } from '../../../../../core/services/translation-loader.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {DepartmentsService} from '../../../../../main/services/departments-service/departments-service.service';
import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';
import { ActivatedRoute } from '@angular/router';
import { NgForm, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CompaniesService } from '../../../../../main/services/companies-service/companies-service.service';

@Component({
    selector   : 'admin-departments',
    templateUrl: './admin-departments.component.html',
    styleUrls  : ['./admin-departments.component.scss']
})
export class AdminDepartmentsComponent
{
    loginForm: FormGroup;
    loginFormErrors: any;

    companyID;
    rows: any[];
    editing = {};
    editData = {}

    loadingIndicator = true;
    reorderable = true;
    companyNameUrl="";

    departmentsService;

    constructor(private route: ActivatedRoute,private translationLoader: FuseTranslationLoaderService, public dialog: MatDialog, public depService: DepartmentsService,
                private fuseConfig: FuseConfigService,private formBuilder: FormBuilder,private companiesService: CompaniesService)
    {
        this.translationLoader.loadTranslations(english, turkish);
        this.departmentsService = depService;
        this.loginFormErrors = {
            //email   : {},
            name: {}
        };
    }

    ngOnInit()
    {
       
        this.loginForm = this.formBuilder.group({
            //email   : ['', [Validators.required, Validators.email]],
            name: ['', Validators.required]
        });
        this.companyID = null;
        if (this.route.snapshot.params['companyID']){
            this.companyID = parseInt(this.route.snapshot.params['companyID'], 10);
          }
        if (this.companyID){
            this.departmentsService.getCompanyDepartments(this.companyID)
            .subscribe(
                (response) => {  
                    console.log('response = ', response);
                    if (response){
                        this.rows = response;
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
            this.companiesService.getUserCompanies()
            .subscribe(
                (companies) => {
                    console.log('response = ', companies);
                    if (companies){
                        companies.forEach(company => {
                            if(company.id==this.companyID){
                                this.companyNameUrl  = company.name;
                            }
                        });
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
        }
        
       // this.companyNameUrl="karmi";
        // const settings = Object.assign({}, this.fuseConfig.defaultSettings);
        // if (settings && settings.layout) {
        //     // settings.layout.navigationFolded = true;
        //     settings.layout.navigation = 'left';
        //     settings.layout.navigationFolded = false;
        //     this.fuseConfig.setSettings(settings);
        // }
    }

    openDialog(): void
    {
        let dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
            width: '250px',
            data : {
            }
        });

        dialogRef.afterClosed().subscribe(departmentName => {
            if (departmentName){
                this.departmentsService.addDepartment(this.companyID, departmentName)
                    .subscribe(
                        (response) => {
                            console.log('response dep = ', response);
                            if (response.isSucceeded){
                                this.rows.push({
                                    'name': departmentName,
                                    'id':response.department_id,
                                });
                                this.rows = [...this.rows];
                            }
                        },
                        (error) => {
                            console.log('error = ', error);
                        }
                    );
                
            }
        });
    }

    enableEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            this.editData[rowIndex] = {};
            this.editData[rowIndex]['name'] = this.rows[rowIndex]['name'];
            this.editing[rowIndex] = true;
        }
    }

    cancelEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            delete this.editData[rowIndex];
            this.editing[rowIndex] = false;
        }
    }

    // updateRow(rowIndex) {
     
    //     if (this.editData[rowIndex]){
    //         this.rows[rowIndex]['name'] = this.editData[rowIndex]['name'];
    //         this.rows = [...this.rows];
    //     }
    //     this.cancelEditRow(rowIndex);
    //     this.departmentsService.updateDepartment(this.rows[rowIndex]);
        
    //   }
    updateRow(rowIndex) {
     
        if (this.editData[rowIndex]){
            
            console.log('this.rows[rowIndex]ID] = ', this.rows[rowIndex]['id']);
            console.log('this.editData[rowIndex][name] = ', this.editData[rowIndex]['name'] );//this.editData[rowIndex]['name']
            this.departmentsService.updateDepartment(this.companyID,this.rows[rowIndex]['id'],this.editData[rowIndex])
            .subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response.isSucceeded){
                        this.rows[rowIndex]['name'] = this.editData[rowIndex]['name'];
                        this.rows = [...this.rows];
                        this.cancelEditRow(rowIndex);
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
            
        }

      }


    deleteRow(rowIndex){
        const result = confirm("Want to delete?");
        if (result) {
            this.departmentsService.deleteDepartment(this.rows[rowIndex]['id']).
            subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response.isSucceeded){
                        this.rows = this.rows.filter(item => item.id !== this.rows[rowIndex]['id']);
                        this.rows = [...this.rows];
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
        }
    }


}



@Component({
    selector   : 'add-department-dialog',
    templateUrl: 'add-department-dialog.html'
})
export class DialogOverviewExampleDialog
{

    constructor(
        public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
        @Inject(MAT_DIALOG_DATA) public data: any
    )
    {
    }

    onNoClick(): void
    {
        this.dialogRef.close();
    }

}


