import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { SharedModule } from '../../../../../core/modules/shared.module';

import { AdminDepartmentsComponent, DialogOverviewExampleDialog } from './admin-departments.component';

import { AuthGuard } from '../../../../services/auth-guard-service/auth-guard.service';

const routes = [
    {
        path: 'admin/company/:companyID/departments',
        component: AdminDepartmentsComponent,
        canActivate: [AuthGuard]
    }
];

@NgModule({
    declarations: [
        AdminDepartmentsComponent,
        DialogOverviewExampleDialog
    ],
    entryComponents: [DialogOverviewExampleDialog],
    imports     : [
        SharedModule,
        RouterModule.forChild(routes)
    ],
    exports     : [
        AdminDepartmentsComponent
    ]
})

export class AdminDepartmentsModule
{
}



