import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { SharedModule } from '../../../../../core/modules/shared.module';

import { AdminTeamsComponent , DialogOverviewExampleDialog} from './admin-teams.component';

import { AuthGuard } from '../../../../services/auth-guard-service/auth-guard.service';

const routes = [
    {
        path: 'admin/company/:companyID/teams',
        component: AdminTeamsComponent,
        canActivate: [AuthGuard]
    }
];

@NgModule({
    declarations: [
        AdminTeamsComponent,
        DialogOverviewExampleDialog
    ],
    entryComponents: [DialogOverviewExampleDialog],
    imports     : [
        SharedModule,
        RouterModule.forChild(routes)
    ],
    exports     : [
        AdminTeamsComponent
    ]
})

export class AdminTeamsModule
{
}



