import { Component, Inject, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FuseTranslationLoaderService } from '../../../../../../core/services/translation-loader.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FloorsService } from '../../../../../../main/services/floors-service/floors-service.service';
import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';
import { BuildingsService } from '../../../../../../main/services/buildings-service/buildings-service.service';

import { ActivatedRoute } from '@angular/router';
import { CompaniesService } from '../../../../../../main/services/companies-service/companies-service.service';
@Component({
    selector   : 'admin-floors',
    templateUrl: './admin-floors.component.html',
    styleUrls  : ['./admin-floors.component.scss']
})
export class AdminFloorsComponent implements OnInit
{

    companyID;
    rows;
    editing = {};
    editData = {};

    loadingIndicator = true;
    reorderable = true;

    floorName: string;
    Buildings;
    companyNameUrl="";

    constructor(private translationLoader: FuseTranslationLoaderService, private floorsService: FloorsService, 
        public dialog: MatDialog, public router: Router, private buildingsService: BuildingsService
        ,private companiesService: CompaniesService,
        private route: ActivatedRoute)
    {
        this.translationLoader.loadTranslations(english, turkish);

        this.companyID = 9;

        this.rows = [];

            // this.rows = this.floorsService.getCompanyFloor(123);
            // this.Buildings = this.buildingsService.getCompanyBuildings(123);
    }

    ngOnInit()
    {
        this.companyID = null;
        if (this.route.snapshot.params['companyID']){
            this.companyID = parseInt(this.route.snapshot.params['companyID'], 10);
          }
        if (this.companyID){
            this.floorsService.getCompanyFloors(this.companyID)
                .subscribe(
                    (response) => {
                        console.log('response = ', response);
                        if (response) {
                            this.rows = response;
                        }
                        this.loadingIndicator = false;
                    },
                    (error) => {
                        this.loadingIndicator = false;
                        console.log('error = ', error);
                    }
                );

            this.buildingsService.getCompanyBuildings(this.companyID)
                .subscribe(
                    (response) => {
                        console.log('response = ', response);
                        if (response) {
                            this.Buildings = response;
                        }
                    },
                    (error) => {
                        console.log('error = ', error);
                    }
            );
            this.companiesService.getUserCompanies()
            .subscribe(
                (companies) => {
                    console.log('response = ', companies);
                    if (companies){
                        companies.forEach(company => {
                            if(company.id==this.companyID){
                                this.companyNameUrl  = company.name;
                            }
                        });
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
        }
    }

    openDialog(): void
    {
        const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
            width: '250px',
            data : {
                Buildings: this.Buildings
            }
        });

        dialogRef.afterClosed().subscribe(result => {
            if (result){
                console.log("result: ", result);
                this.floorsService.addFloor(result.floorName, result.selectedBuilding.id,result.roomsNumber, result.file, null)
                    .subscribe(
                        (response) => {
                            console.log('response = ', response);
                            if (response) {
                                // this.rows = response;
                                this.rows.push({
                                    "id": response,
                                    "name": result.floorName,
                                    "building": result.selectedBuilding,
                                    "imgFloor": result.imgFloor,
                                    "roomsNumber":result.roomsNumber,
                                });
                                this.rows = [...this.rows];
                                this.router.navigateByUrl('admin/floor/edit/' + response.toString());
                            }
                            // this.loadingIndicator = false;
                        },
                        (error) => {
                            // this.loadingIndicator = false;
                            console.log('error = ', error);
                        }
                    );
                // const idNewFloor = this.floorsService.addFloor(result.floorName, result.selectedBuilding, result.imgFloor, null);

                // this.router.navigateByUrl('admin/floor/edit/' + idNewFloor);
                
            }
        });
    }

    addNewFloor(){
        //this.router.navigateByUrl('/admin/floors/new');
        //window.location.href="/admin/floors/new";
    }

    enableEditRow(rowIndex){
        console.log("ID index = ",'admin/floor/edit/'+this.rows[rowIndex]['id'])
        this.router.navigateByUrl('admin/floor/edit/'+this.rows[rowIndex]['id']);
        // if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
        //     this.editData[rowIndex] = {};
        //     this.editData[rowIndex]['name'] = this.rows[rowIndex]['name'];
        //     this.editing[rowIndex] = true;
        // }
    }

    cancelEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            delete this.editData[rowIndex];
            this.editing[rowIndex] = false;
        }
    }

    updateRow(rowIndex) {
        console.log("editData = ",this.editData)
        console.log('inline editing rowIndex', rowIndex)
        if (this.editData[rowIndex]){
            this.rows[rowIndex]['name'] = this.editData[rowIndex]['name'];
            this.rows = [...this.rows];
        }
        this.cancelEditRow(rowIndex);
        console.log('UPDATED!', this.rows[rowIndex]['name']);
      }

      deleteRow(rowIndex){
        const result = confirm("Want to delete?");
        if (result) {
            this.floorsService.deleteFloor(this.rows[rowIndex]['id']).
            subscribe(
                (response) => {
                    console.log('response = ', response);
                    if ((response as any).isSucceeded){
                        this.rows = this.rows.filter(item => item.id !== this.rows[rowIndex]['id']);
                        this.rows = [...this.rows];
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
        }
    }



}

@Component({
    selector   : 'add-floor-dialog',
    templateUrl: 'add-floor-dialog.html'
})
export class DialogOverviewExampleDialog
{

    constructor(
        public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
        @Inject(MAT_DIALOG_DATA) public data: any
    )
    {
    }

    onNoClick(): void
    {
        this.dialogRef.close();
    }

    fileChange(event) {
        console.log('fileChange. event = ', event);
        const fileList: FileList = event.target.files;
        if (fileList.length > 0) {
            const file: File = fileList[0];
            this.data.file = file;
        }
    }

}


