import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { SharedModule } from '../../../../../../core/modules/shared.module';

import { AdminFloorsComponent , DialogOverviewExampleDialog} from './admin-floors.component';

import { AuthGuard } from '../../../../../services/auth-guard-service/auth-guard.service';

const routes = [
    {
        path: 'admin/company/:companyID/floors',
        component: AdminFloorsComponent,
        canActivate: [AuthGuard]
    }
];

@NgModule({
    declarations: [
        AdminFloorsComponent,
        DialogOverviewExampleDialog
    ],
    entryComponents: [DialogOverviewExampleDialog],
    imports     : [
        SharedModule,
        RouterModule.forChild(routes)
    ],
    exports     : [
        AdminFloorsComponent
    ]
})

export class AdminFloorsModule
{
}



