import { FuseNavigationModelInterface } from '../core/components/navigation/navigation.model';

export class FuseNavigationModel implements FuseNavigationModelInterface
{
    public model: any[];

    constructor()
    {
        this.model = [
            {
                'id'      : 'florrPlan',
                'title'   : 'Floor Plan',
                'translate': 'NAV.FLOOR_PLAN',
                'type'    : 'group',
                'children': [
                    {
                        'id'   : 'placeEmployee',
                        'title': 'Place Employee',
                        'translate': 'NAV.PLACE_EMPLOYEE.TITLE',
                        'type' : 'item',
                        'icon' : 'adjust',
                        'url'  : '/floor-plan/place-employee'
                    },
                ]
            }
        ];
    }
}



