import { Component, OnInit } from '@angular/core';
import { NgForm, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FuseConfigService } from '../../../../core/services/config.service';
import { fuseAnimations } from '../../../../core/animations';

import { AuthService } from '../../../../main/services/auth-service/auth.service';

@Component({
    selector   : 'fuse-login',
    templateUrl: './login.component.html',
    styleUrls  : ['./login.component.scss'],
    animations : fuseAnimations
})
export class FuseLoginComponent implements OnInit
{
    loginForm: FormGroup;
    loginFormErrors: any;

    constructor(
        private fuseConfig: FuseConfigService,
        private formBuilder: FormBuilder,
        private authService: AuthService
    )
    {
        this.fuseConfig.setSettings({
            layout: {
                navigation: 'none',
                toolbar   : 'none',
                footer    : 'none'
            }
        });

        this.loginFormErrors = {
            email   : {},
            password: {}
        };
    }

    ngOnInit()
    {
        this.loginForm = this.formBuilder.group({
            email   : ['', [Validators.required, Validators.email]],
            password: ['', Validators.required]
        });

        this.loginForm.valueChanges.subscribe(() => {
            this.onLoginFormValuesChanged();
        });
    }

    onLoginFormValuesChanged()
    {
        for ( const field in this.loginFormErrors )
        {
            if ( !this.loginFormErrors.hasOwnProperty(field) )
            {
                continue;
            }

            // Clear previous errors
            this.loginFormErrors[field] = {};

            // Get the control
            const control = this.loginForm.get(field);

            if ( control && control.dirty && !control.valid )
            {
                this.loginFormErrors[field] = control.errors;
            }
        }
    }

    onSignin(form: NgForm) {
       
        const email = form.value.email;
        const password = form.value.password;
        console.log('onSignin. email = ' + email + ' password = ' + password);

        this.authService.login(email, password).subscribe(
            (response) => {
                if (response.isSucceeded && response.token) {
                    // this.token = response.token; 
                    this.authService.storeToken(response.token);
                    this.authService.userAuthenticated.emit({ userDetails: response.userDetails});
                    this.authService.ruotToEmp();
                    } else {
                    alert('Incorrect email or password. Please try again');
                    }
            },
            (error) => {
                // this.loadingIndicator = false;
                console.log('error = ', error);
            }

            
        );
    }
}



