import { Component, Inject, OnInit } from '@angular/core';
import { FuseTranslationLoaderService } from '../../../../../core/services/translation-loader.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { BuildingsService } from '../../../../../main/services/buildings-service/buildings-service.service';
import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';
import { ActivatedRoute } from '@angular/router';
import { CompaniesService } from '../../../../../main/services/companies-service/companies-service.service';

@Component({
    selector   : 'admin-buildings',
    templateUrl: './admin-buildings.component.html',
    styleUrls  : ['./admin-buildings.component.scss']
})
export class AdminBuildingsComponent implements OnInit
{
    companyID;
    rows: any[];
    editing = {};
    editData = {};

    loadingIndicator = true;
    reorderable = true;

    buildingName: string;
    buildingsService;
    companyNameUrl="";

    constructor(private route: ActivatedRoute,private translationLoader: FuseTranslationLoaderService,
         public dialog: MatDialog, public service:BuildingsService
         ,private companiesService: CompaniesService)
    {
        this.translationLoader.loadTranslations(english, turkish);
        this.buildingsService = service;
        // this.rows = this.buildingsService.getCompanyBuildings(1);
        //this.companyID = 9;
        this.rows = [];
       
    }

    ngOnInit()
    {
        this.companyID = null;
        if (this.route.snapshot.params['companyID']){
            this.companyID = parseInt(this.route.snapshot.params['companyID'], 10);
          }
        if (this.companyID){
            this.buildingsService.getCompanyBuildings(this.companyID)
            .subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response) {
                        this.rows = response;
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
            this.companiesService.getUserCompanies()
            .subscribe(
                (companies) => {
                    console.log('response = ', companies);
                    if (companies){
                        companies.forEach(company => {
                            if(company.id==this.companyID){
                                this.companyNameUrl  = company.name;
                            }
                        });
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
        }

    }

    openDialog(): void
    {
        let dialogRef = this.dialog.open(AddBuildingDialog, {
            width: '250px',
            data : {
                buildingName: this.buildingName
            }
        });

        dialogRef.afterClosed().subscribe(newBuilding => {
            if (newBuilding){
                const name = newBuilding.name;
                const city = newBuilding.city;
                const address = newBuilding.address;
                const zip = newBuilding.zip;
                this.buildingsService.addBuilding(this.companyID, name, city, address, zip)
                    .subscribe(
                        (response) => {
                            console.log('response = ', response);
                            if (response.isSucceeded) {
                                this.rows.push({
                                    'name': name,
                                    'city': city,
                                    'address': address,
                                    'zip': zip,
                                    'id':response.building_id,
                                });
                                this.rows = [...this.rows];
                            }
                        },
                        (error) => {
                            console.log('error = ', error);
                        }
                    );

                
            }
        });
    }

    enableEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            this.editData[rowIndex] = {};
            this.editData[rowIndex]['name'] = this.rows[rowIndex]['name'];
            this.editData[rowIndex]['city'] = this.rows[rowIndex]['city'];
            this.editData[rowIndex]['address'] = this.rows[rowIndex]['address'];
            this.editData[rowIndex]['zip'] = this.rows[rowIndex]['zip'];
            this.editing[rowIndex] = true;
        }
    }

    cancelEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            delete this.editData[rowIndex];
            this.editing[rowIndex] = false;
        }
    }

    // updateRow(rowIndex) {
    //     console.log("editData = ",this.editData)
    //     console.log('inline editing rowIndex', rowIndex)
    //     if (this.editData[rowIndex]){
    //         this.rows[rowIndex]['name'] = this.editData[rowIndex]['name'];
    //         this.rows = [...this.rows];
    //     }
    //     this.cancelEditRow(rowIndex);
    //     console.log('UPDATED!', this.rows[rowIndex]['name']);
    //   }

      updateRow(rowIndex) {
     
        if (this.editData[rowIndex]){
            
            console.log('this.rows[rowIndex]ID] = ', this.rows[rowIndex]['id']);
            console.log('this.editData[rowIndex][name] = ', this.editData[rowIndex]['name'] );//this.editData[rowIndex]['name']
            this.buildingsService.updateBuilding(this.companyID,this.rows[rowIndex]['id'],this.editData[rowIndex])
            .subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response.isSucceeded){
                        this.rows[rowIndex]['name'] = this.editData[rowIndex]['name'];
                        this.rows[rowIndex]['city'] = this.editData[rowIndex]['city'];
                        this.rows[rowIndex]['address'] = this.editData[rowIndex]['address'];
                        this.rows[rowIndex]['zip'] = this.editData[rowIndex]['zip'];
                        this.rows = [...this.rows];
                        this.cancelEditRow(rowIndex);
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
            
        }

      }

      deleteRow(rowIndex){
        const result = confirm("Want to delete?");
        if (result) {
            this.buildingsService.deleteBuilding(this.rows[rowIndex]['id']).
            subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response.isSucceeded){
                        this.rows = this.rows.filter(item => item.id !== this.rows[rowIndex]['id']);
                        this.rows = [...this.rows];
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
        }
    }
      


}



@Component({
    selector   : 'add-building-dialog',
    templateUrl: 'add-building-dialog.html'
})
export class AddBuildingDialog
{

    constructor(
        public dialogRef: MatDialogRef<AddBuildingDialog>,
        @Inject(MAT_DIALOG_DATA) public data: any
    )
    {
    }

    onNoClick(): void
    {
        this.dialogRef.close();
    }

}


