import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class DepartmentsService {

  baseURL: string;


  getCompanyDepartments(companyID){
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url, 
        JSON.stringify({
        'controller': 'read departments',
        'company_id': companyID
      })
    );
  }

  addDepartment(companyID, departmentName){
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'add department',
        'company_id': companyID,
        'name': departmentName
      })
    );
  }

  updateDepartment(companyID,departmentID, dataDepartmentForEdit){
    const url = this.baseURL + '/models/edit.php';
    console.log("dataDepartmentForEdit",dataDepartmentForEdit);
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'edit department',
        'company_id': companyID,
        'department_id': departmentID,
        'department_name': dataDepartmentForEdit.name,
      })
    ); 
  } 

  deleteDepartment(departmentID){ 
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'delete department',
        'department_id': departmentID,
      })
    ); 
  }

  //updateDepartment(dataDepartmentForEdit){
    // let updateItem = this.departments.find(this.findIndexToUpdate, dataDepartmentForEdit.ID);
    // let index = this.departments.indexOf(updateItem);
    // this.departments[index] = dataDepartmentForEdit;

  //}

  findIndexToUpdate(dataDepartmentForEdit) { 
        // return dataDepartmentForEdit.ID === this;
  }



  constructor(private httpClient: HttpClient) { 
    this.baseURL =       '//35.231.189.174'; //  '//' + window.location.hostname;
  }

}



