import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class PlaceEmployeeService {

  baseURL: string;
 
  // Events
  public employeeDroped = new EventEmitter<number>();
  public employeePlaced = new EventEmitter<{ employeeID: number, chairID: number}>();
  public chairEmpty = new EventEmitter<number>();


  placeEmployee(employeeID: number, chairID: number, action: string){
    const formData: FormData = new FormData();
    if (action) {
      action = action.toLowerCase();
    }
    if (employeeID && chairID && action === 'add' || action === 'replace'){
      formData.append('point_id', chairID.toString());
      formData.append('employee_id', employeeID.toString());
      formData.append('action', action);
    }

    const url = this.baseURL + '/models/setPlace.php';
    return this.httpClient.post(url, formData);
  }

  constructor(private httpClient: HttpClient) {
       this.baseURL =       '//35.231.189.174'; //  '//' + window.location.hostname;

   }

}



