import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { BrowserModule } from '@angular/platform-browser';

import { SharedModule } from '../../../../core/modules/shared.module';

import { AuthGuard } from '../../../services/auth-guard-service/auth-guard.service';
import { PlaceEmployeeComponent, MultiPlacesDialogComponent } from './place-employee.component';
import { FloorMapModule } from '../../components/floor-map/floor-map.module';

const routes = [
    {
        path     : 'floor-plan/place-employee',
        component: PlaceEmployeeComponent,
        canActivate: [AuthGuard]
    }
];

@NgModule({
    declarations: [
        PlaceEmployeeComponent,
        MultiPlacesDialogComponent
    ],
    imports     : [
        SharedModule,
        RouterModule.forChild(routes),
        BrowserModule,
        FloorMapModule
    ],
    entryComponents: [MultiPlacesDialogComponent],
    exports     : [
        PlaceEmployeeComponent
    ],
    bootstrap: [ PlaceEmployeeComponent ]
})

export class PlaceEmployeeModule
{
}



