import { OnInit, OnDestroy, NgModule, Component, Inject, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import {DatatableComponent} from '@swimlane/ngx-datatable';
import { Subscription } from 'rxjs/Subscription';

import { FuseTranslationLoaderService } from '../../../../core/services/translation-loader.service';
import { FuseConfigService } from '../../../../core/services/config.service';
import {EmployeesService} from '../../../../main/services/employees-service/employees-service.service';
import {FloorsService} from '../../../../main/services/floors-service/floors-service.service';
import { CompaniesService } from '../../../../main/services/companies-service/companies-service.service';
import { BuildingsService } from '../../../../main/services/buildings-service/buildings-service.service';
import { PlaceEmployeeService } from '../../../../main/services/place-employee-service/place-employee.service';

import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';

@Component({
    // tslint:disable-next-line:component-selector
    selector   : 'place-employee',
    templateUrl: './place-employee.component.html',
    styleUrls  : ['./place-employee.component.scss']
})
export class PlaceEmployeeComponent implements OnInit, OnDestroy
{

  appDefaultSettings;
  floorChairs; 

  rows;
  loadingIndicator = true;

  dragEmployeeID: number;

  filterData;
  floorName: string;
  
  floor;
  selectedCompany = 0;
  selectedBuilding = 0 ;
  selectedFloor = 0;
  Buildings;
  Floors;
  Companies: any[];
  isNotBuilding = false;
  isNotFloor = false;

  employeeDropedSubscription: Subscription;
  copyRows = [];

  @ViewChild(DatatableComponent) table: DatatableComponent;

  constructor(private translationLoader: FuseTranslationLoaderService, 
              private fuseConfig: FuseConfigService,
              private employeesService: EmployeesService, 
              private floorsService: FloorsService,
              private buildingsService: BuildingsService, 
              private companiesService: CompaniesService,
              private placeEmployeeService: PlaceEmployeeService, 
              public dialog: MatDialog)
  {  
      this.translationLoader.loadTranslations(english, turkish);
      

      this.companiesService.getUserCompanies()
          .subscribe(
              (companies) => {
                  this.Companies = companies;
                  if (this.Companies.length > 0){
                      this.selectedCompany = this.Companies[0].id;
                      this.updateBuildingsFilter();
                      this.updateEmployees();
                  } else {
                      this.Buildings = [];
                      this.selectedBuilding = null;
                      this.rows = [];
                      this.copyRows = this.rows;
                  }
              },
                (error) => {
                    console.log('error = ', error);
                });
     
  }


    placeEmployee(action, employee, chair){
        const employeeID = employee.id;
        const chairID = chair.id;
        this.placeEmployeeService.placeEmployee(employeeID, chairID, action)
            .subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response) {
                        this.updateEmployees();
                        this.updateFloorChairs();
                        // this.onChangeFloor(this.Floors[0].id, false); // Update floor chairs
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
       //  this.placeEmployeeService.placeEmployee(employeeID, chairID);


        const location = chair.location;
        const prevEmployeeID = chair.employeeID;
        const newEmployeeName = employee['firstName'];

        if (prevEmployeeID) {
            // The chair already placed by another user.
            const prevEmployee = this.rows.find(o => o.id === prevEmployeeID);

                if (prevEmployee) {
                    const index: number = prevEmployee.points.indexOf(chairID);
                    if (index !== -1) {
                        prevEmployee.points.splice(index, 1);
                        prevEmployee.locations.splice(index, 1);
                    }
                    // const prevLocation = prevEmployee.locationsIDs.find(o => o.ID === chairID);
                    // console.log('** prevLocation = ', prevLocation);
                    // prevEmployee.locations = prevEmployee.locations.filter(item => item.ID !== prevLocation.ID);  
                }

        }
        if (action === 'REPLACE'){
            const locationsLength = employee['points'].length;
            for (let index = 0; index < locationsLength; index++) {
                console.log('index = ' + index);
                employee['locations'].pop();
                const freeChairID = employee['points'].pop();
                console.log('freeChairID = ' + freeChairID);
                this.placeEmployeeService.chairEmpty.emit(freeChairID);
            }

        }
        employee['locations'].push(location);
        employee['points'].push(chairID);
        employee['locations'] = [...employee['locations']];
        // console.log('*** loorChairs = %o', this.floorChairs);
        // chair.employeeID = 100;
        // console.log('*** floorChairs = %o', this.floorChairs);
        this.placeEmployeeService.employeePlaced.emit({ employeeID: employeeID, chairID: chairID});
        // console.log('before update rows: ' + new Date().toString());

        this.rows = [... this.rows];
        this.copyRows = this.rows;
        employee['locations'] = [...employee['locations']];
        
  }


updateFloorChairs(){
    if (this.selectedFloor){
        this.floorsService.getFloor(this.selectedFloor)
            .subscribe(
                (floor) => {
                    if (floor) {
                        this.floor = floor;
                        this.floorChairs = this.floor['chairs'];
                    }
                },
                (error) => {
                    console.log('error = ', error);
                });
    }

}


validatePlaceEmployee(employeeID: number, chairID: number) {
    return new Promise((resolve, reject) => {
        console.log('validatePlaceEmployee.');
        console.log('employeeID = ', employeeID);
        console.log('chairID = ', chairID);
        console.log('this.floorChairs = ', this.floorChairs);
        console.log('this.rows = ', this.rows);
        const resolveResponse = {
            action: null,
            employee: null,
            chair: null
        };

        // this.placeEmployeeService.placeEmployee(employeeID, chairID);
        const chair = this.floorChairs.find(o => o.id.toString() === chairID.toString());
        const employee = this.rows.find(o => o.id === employeeID);

        let location = null;
        if (!chair || !employee) {
            // Can't find the dragged employee or the chair
            console.log('Not valid. chair = %o, employee = %o', chair, employee);
            return reject('employee or chir not found');
        }
        resolveResponse.employee = employee;
        resolveResponse.chair = chair;

        location = chair.location;
        const prevEmployeeID = chair.employeeID;
        const newEmployeeName = employee['firstName'] + ' ' + employee['lastName'];
        console.log('employee = %o', employee);

        // Check if the chair already placed
        const prevEmployee = this.rows.find(o => o.id === prevEmployeeID);
        if (prevEmployee){
            // The chair already placed
            if (prevEmployeeID && (prevEmployeeID === employee.id)) {
                // Placed by the same user
                alert('This chair is already placed by ' + newEmployeeName);
                return reject('The chair aleady placed by the same user');
            }
            // Placed by another user
            const prevEmployeeName = prevEmployee['firstName'] + ' ' + prevEmployee['lastName'];
            if (!confirm('This chair is already placed by ' + prevEmployeeName + '.\r\nAre you sure you want to place ' + newEmployeeName + ' instead?')) {
                return reject('The chair placed by another user. User cancelled.');
            }
        }

        // Check if the employee have more then one location
        const newEmployeeLocations = employee['locations'];
        if (newEmployeeLocations.length === 0) {
            resolveResponse.action = 'ADD';
            return resolve(resolveResponse);
        } else {
            // User have more then one location. 
            // Checking which action to perform (add more/replace/cancel)
            const dialogRef = this.dialog.open(MultiPlacesDialogComponent, {
                width: '700px',
                data: {
                    employeeName: newEmployeeName,
                    locations: newEmployeeLocations
                }
            });

            dialogRef.afterClosed().subscribe(result => {
                if (result) {
                    if (result === 'ADD') {
                        // return callback(true, 'ADD', employee, chair).bind(this);
                        resolveResponse.action = 'ADD';
                        return resolve(resolveResponse);
                    } else if (result === 'REPLACE') {
                        // return callback(true, 'REPLACE', employee, chair).bind(this);
                        resolveResponse.action = 'REPLACE';
                        return resolve(resolveResponse);
                    } else {
                        // Cancel
                        // return callback(false).bind(this);
                        return reject('User cancelled.');
                    }
                }
            });
        } 

    });
}


  ngOnInit()
  {
      

      // Save app default settings
      this.appDefaultSettings = JSON.parse(JSON.stringify(this.fuseConfig.defaultSettings));
      const settings = JSON.parse(JSON.stringify(this.appDefaultSettings));
      if (settings && settings.layout){
          // Change settings to fold the left navigator
          settings.layout.navigationFolded = true;
          this.fuseConfig.setSettings(settings);
      }




      this.employeeDropedSubscription = this.placeEmployeeService.employeeDroped
        .subscribe(
            (chairID: number = 0) => {
                const employeeID = this.dragEmployeeID;
                console.log('$%$%# Place employee: employeeDroped. chairID=' + chairID);
                // this.validatePlaceEmployee(employeeID, chairID, this.validateEmployeeCallback);
                this.validatePlaceEmployee(employeeID, chairID).then(
                    (data: {action: string, employee: object, chair: object}) => {
                        console.log('then, data = ' , data); 
                        this.placeEmployee(data.action, data.employee, data.chair);
                    },
                    (err) => { console.log('Validation failed. Error: ' + err); }
                );
                
                
                // this.promise.then(
                //     (data) => { console.log(data); },
                //     (err) => { console.log(err); }
                // );



                // this.validatePlaceEmployee(employeeID, chairID, 
                //     (isValid, action, employee, chair) => {
                //     if (isValid){
                //         console.log('valid! employee=%o, chair= %o', employee, chair);
                //         this.placeEmployee(action, employee, chair);
                //     } else {
                //         console.log('not valid!');
                //     }

                // });

                // item.addEventListener('drop', e => {
                //     if (e.target) {
                //         let targetElement: any;
                //         targetElement = e.target;
                //         targetElement.classList.remove('employee-drag-over');
                //         let chairID = targetElement.className.baseVal.split(' ').find(str => str.includes('place')).split('-')[1];
                //         chairID = parseInt(chairID, 10);
                //         this.placeEmployeeService.employeeDroped.emit(chairID);
                //     }
                // });
                
                

            }
        );


  }




    private onDragEmployee(e: DragEvent , employeeID){
       this.dragEmployeeID = employeeID;
    }

    onDropEmployee(event: {dropEvent: DragEvent, chairID: number}){


    }

    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
        this.rows.filter = filterValue;
      }

    updateFilter(event) {
        this.copyRows = this.rows;
        const val = event.target.value.toLowerCase();
    
        // filter our data
        const temp = this.rows.filter(function(d) {
          return d.name.toLowerCase().indexOf(val) !== -1 || !val;
        });
    
        // update the rows
        this.copyRows = temp;
        // Whenever the filter changes, always go back to the first page
        this.table.offset = 0;
      }

    // Update employees according to selected company
    updateEmployees() {
        if (this.selectedCompany) {
            this.employeesService.getCompanyEmployees(this.selectedCompany)
                .subscribe(
                    (employees) => {
                        this.rows = employees;
                        this.copyRows = this.rows;
                        this.loadingIndicator = false;
                    },
                    (error) => {
                        console.log('error = ', error);
                        this.loadingIndicator = false;
                    });
        }
    }

    // Update floors filter according to selected building
    updateFloorsFilter() {
        if (this.selectedBuilding) {
            this.floorsService.getBuildingFloor(this.selectedBuilding)
                .subscribe(
                    (floors) => {
                        this.Floors = floors;
                        if (this.Floors.length > 0) {
                            this.selectedFloor = this.Floors[0].id;
                            this.onChangeFloor(this.Floors[0].id, false);
                            // this.floor = this.floorsService.getFloor(this.selectedFloor);
                            // this.floorChairs = this.floor['chairs'];
                            // this.floor = this.floorsService.getFloor(this.selectedFloor);
                            // this.floorChairs = this.floor['chairs'];
                            this.isNotFloor = false;
                        }
                        else {
                            console.log('not floor');
                            this.isNotFloor = true;
                        }
                    },
                    (error) => {
                        console.log('error = ', error);
                    });
        }
    }

    // Update buildings filter according to selected company
    updateBuildingsFilter() {
        if (this.selectedCompany) {
            this.buildingsService.getCompanyBuildings(this.selectedCompany)
                .subscribe(
                    (buildings) => {
                        this.Buildings = buildings;
                        if (this.Buildings.length > 0) {
                            this.selectedBuilding = this.Buildings[0].id;
                            this.updateFloorsFilter();
                        } else {
                            this.selectedFloor = null;
                            this.Floors = [];
                        }
                    },
                    (error) => {
                        console.log('error = ', error);
                    });
        }
    }



    onChangeCompany(companyID) {
        if (companyID){
            this.selectedCompany = JSON.parse(companyID);
            this.updateBuildingsFilter();
            this.updateEmployees();
        }

  }

    onChangeBuilding(buildingID) {
        if (buildingID){
            this.selectedBuilding = JSON.parse(buildingID);
            console.log('this.selectedDevice = ', this.selectedBuilding);
            this.updateFloorsFilter();
        }
      
      

  }
  onChangeFloor(floorID, jsonFormat) {    
      if (jsonFormat){
          this.selectedFloor = JSON.parse(floorID);
      } else {
          this.selectedFloor = floorID;
      }
      
      console.log('this.selectedFloor = ', this.selectedFloor);
      this.floorsService.getFloor(this.selectedFloor)
          .subscribe(
              (floor) => {
                  if (floor){
                      this.floor = floor;
                      this.floorChairs = this.floor['chairs'];
                  }
              },
          (error) => {
              console.log('error = ', error);
          });



}

    ngOnDestroy() {
        this.employeeDropedSubscription.unsubscribe();
        this.fuseConfig.setSettings(this.appDefaultSettings); // return to app default settings
    }
       

}



@Component({
    // tslint:disable-next-line:component-selector
    selector: 'multi-places-dialog',
    templateUrl: 'multi-places-dialog.html',
    styleUrls: ['multi-places-dialog.scss']
})
export class MultiPlacesDialogComponent {

    constructor(
        public dialogRef: MatDialogRef<MultiPlacesDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any
    ) {
    }

    onAddMore(): void {
        this.dialogRef.close('ADD');
    }
    onPlaceInsted(): void {
        this.dialogRef.close('REPLACE');
    }
    onNoClick(): void {
        this.dialogRef.close('CANCEL');
    }

}



