import { NgModule, Component, OnInit, OnDestroy } from '@angular/core';
import { MouseEvent } from '@agm/core';
import { MapTypeId } from '@agm/core/services/google-maps-types';
import { FuseTranslationLoaderService } from '../../../../../../core/services/translation-loader.service';
import { FuseConfigService } from '../../../../../../core/services/config.service';
import { FloorsService } from '../../../../../../main/services/floors-service/floors-service.service';

import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';
import { ActivatedRoute } from '@angular/router';



@Component({
    selector   : 'edit-floor',
    templateUrl: './edit-floor.component.html',
    styleUrls  : ['./edit-floor.component.scss']
})
export class EditFloorComponent implements OnInit, OnDestroy
{
    appDefaultSettings;

    floorID: number;
    floor = {
        id: null,
        name: '',
        building: {
          id: null,
          name: ''
        }
    };
    editing = false;
    enableAddChairs: boolean;
    enableAddAreas: boolean;
    editData = {};

    constructor(private route: ActivatedRoute, private translationLoader: FuseTranslationLoaderService, 
      private floorsService: FloorsService, private fuseConfig: FuseConfigService)
    {
        this.translationLoader.loadTranslations(english, turkish);

        this.enableAddChairs = false;
        // this.getFloor = this.floorsService.getFloor(this.floor.id);
        // console.log("floor getFloor = ",this.floorsService.getFloor(this.floor.id));
        // console.log("floor ",this.getFloor);
    }

    ngOnInit(){


      // Save app default settings
      this.appDefaultSettings = JSON.parse(JSON.stringify(this.fuseConfig.defaultSettings));
      const settings = JSON.parse(JSON.stringify(this.appDefaultSettings));
      if (settings && settings.layout) {
        // Change settings to fold the left navigator
        settings.layout.navigationFolded = true;
        this.fuseConfig.setSettings(settings);
      }

      this.floorID = null;
      if (this.route.snapshot.params['id']){
        this.floorID = parseInt(this.route.snapshot.params['id'], 10);
      }

      // this.floor = {};
      if (this.floorID){
        // this.floor = this.floorsService.getFloor(this.floorID);
        this.floorsService.getFloor(this.floorID)
          .subscribe(
            (response) => {
              console.log('response floor = ', response);
              if (response) {
                this.floor = response;
                console.log('this.floor.name = ', this.floor.name);
                // this.rows = response;
              }
              // this.loadingIndicator = false;
            },
            (error) => {
              // this.loadingIndicator = false;
              console.log('error = ', error);
            }
          );
      }
      
      // console.log("floor ", this.floorID);
      // console.log("floor getFloor = ",this.floorsService.getFloor(this.floor.id));
    }

    toggleEnableAddChairs(){
      this.enableAddChairs = !this.enableAddChairs; 
    }

    toggleEnableAddAreas(){
      this.enableAddAreas = !this.enableAddAreas; 
    }

    cancelEditRow(){
          this.editing = false;
  }

  // updateRow() {
  //     this.editing = false;
      
  //   }

  enableEditRow(rowIndex){
        this.editData = {};
        this.editData['name'] = this.floor['name'];
        this.editing = true;
}

    updateRow(rowIndex) {
     
      if (this.editData){
          
      //    console.log('this.rows[rowIndex]ID] = ', this.rows[rowIndex]['id']);
        console.log('this.editData[name] = ', this.editData['name'] ); // this.editData[rowIndex]['name']
          this.floorsService.updateFloor(this.floorID, this.editData['name'])
          .subscribe(
              (response) => {
                  console.log('response = ', response);
                  if (response){
                       this.floor['name'] = this.editData['name'];
                      // this.rows = [...this.rows];
                       this.cancelEditRow();
                  }
              },
              (error) => {
                  console.log('error = ', error);
              }
          );
          
      }

    }

    cancelFloor(){
      
    }

  // google maps zoom level
  // zoom: number = 8;
  
  // // initial center position for the map
  // lat: number = 51.673858;
  // lng: number = 7.815982;
  // pointIconURL = "http://18.195.126.0/img/favicon.ico";

  clickedMarker(label: string, index: number) {
    // console.log(`clicked the marker: ${label || index}`)
  }
  
  mapClicked($event: MouseEvent) {

    // this.getFloor.markers.push({
    //   lat: $event.coords.lat,
    //   lng: $event.coords.lng,
    //   iconUrl: this.pointIconURL,
    //   draggable: true
    // });
   }
  
  // markerDragEnd(m: marker, $event: MouseEvent) {
  //   console.log('dragEnd', m, $event);
  // }

  updateFloor() {
    // this.floorsService.updateFloor(this.floor);

    // console.log("aftar up",this.floorsService.getFloor(1))

  }

  
  ngOnDestroy() {
    this.fuseConfig.setSettings(this.appDefaultSettings); // return to app default settings
  }
 
}

