import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { SharedModule } from '../../../../../core/modules/shared.module';

import { AdminGroupsComponent, DialogOverviewExampleDialog } from './admin-groups.component';

import { AuthGuard } from '../../../../services/auth-guard-service/auth-guard.service';

const routes = [
    {
        path: 'admin/company/:companyID/groups',
        component: AdminGroupsComponent,
        canActivate: [AuthGuard]
    }
];

@NgModule({
    declarations: [
        AdminGroupsComponent,
        DialogOverviewExampleDialog
    ],
    entryComponents: [DialogOverviewExampleDialog],
    imports     : [
        SharedModule,
        RouterModule.forChild(routes)
    ],
    exports     : [
        AdminGroupsComponent
    ]
})

export class AdminGroupsModule
{
}



