import { Component, Inject, OnInit } from '@angular/core';
import { FuseTranslationLoaderService } from '../../../../../core/services/translation-loader.service';
import { DepartmentsService } from '../../../../../main/services/departments-service/departments-service.service';
import { GroupsService } from '../../../../../main/services/groups-service/groups-service.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';
import { ActivatedRoute } from '@angular/router';
import { NgForm, FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { CompaniesService } from '../../../../../main/services/companies-service/companies-service.service';

@Component({
    selector   : 'admin-groups',
    templateUrl: './admin-groups.component.html',
    styleUrls  : ['./admin-groups.component.scss']
})
export class AdminGroupsComponent implements OnInit
{
    groupForm: FormGroup;
    groupFormErrors: any;
    
    companyID;
    rows: any[];
    editing = {};
    editData = {};
    loadingIndicator = true;
    reorderable = true;

    departmentsService;
    groupsService;
    Departments: any[];
    temp: any[];
    companyNameUrl="";
   

    constructor(private route: ActivatedRoute, private translationLoader: FuseTranslationLoaderService, 
        public depService: DepartmentsService, public grService: GroupsService,
        private formBuilder: FormBuilder,public dialog: MatDialog
        ,private companiesService: CompaniesService)
    {
        this.translationLoader.loadTranslations(english, turkish);
        this.departmentsService = depService;
        this.groupsService = grService;
        this.groupFormErrors = {
            //email   : {},
            group_name: {}
        };
       
       
        
    }

   

    ngOnInit()
    {
        this.groupForm = this.formBuilder.group({
            //email   : ['', [Validators.required, Validators.email]],
            group_name: ['', Validators.required]
        });
        // this.groupForm.valueChanges.subscribe(() => {
        //     this.onGroupFormValuesChanged();
        // });

        this.loadingIndicator = false;
        this.companyID = null;
        if (this.route.snapshot.params['companyID']){
            this.companyID = parseInt(this.route.snapshot.params['companyID'], 10);
          }
        if (this.companyID){
            this.groupsService.getCompanyGroups(this.companyID)
            .subscribe(
                (response) => {  
                    console.log('response group = ', response);
                    if (response){
                        this.rows = response;
                        // this.rows[1].department="test";
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
            this.departmentsService.getCompanyDepartments(this.companyID)
            .subscribe(
                (response) => {  
                    console.log('response = ', response);
                    if (response){
                        this.Departments = response;
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
            this.companiesService.getUserCompanies()
            .subscribe(
                (companies) => {
                    console.log('response = ', companies);
                    if (companies){
                        companies.forEach(company => {
                            if(company.id==this.companyID){
                                this.companyNameUrl  = company.name;
                            }
                        });
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
        }

    }

    onGroupFormValuesChanged()
    {
        for ( const field in this.groupFormErrors )
        {
            if ( !this.groupFormErrors.hasOwnProperty(field) )
            {
                continue;
            }

            // Clear previous errors
            this.groupFormErrors[field] = {};

            // Get the control
            const control = this.groupForm.get(field);

            if ( control && control.dirty && !control.valid )
            {
                this.groupFormErrors[field] = control.errors;
            }
        }
    }

    openDialog(): void
    {
        let dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
            width: '250px',
            data : {
                Departments: this.Departments
            }
        });

        dialogRef.afterClosed().subscribe(result => {
            if (result){
                console.log("result this.companyID: ",this.companyID);
                console.log("result result.groupName: ",result.groupName);
                console.log("result result.selectedDepartment.id: ",result.selectedDepartment.id);
                if (this.companyID){
                    this.groupsService.addGroup(this.companyID,result.groupName,result.selectedDepartment.id)
                    .subscribe(
                        (response) => {
                            console.log('response = ', response);
                            if (response.isSucceeded){
                                this.rows.push({
                                    "group_name": result.groupName,
                                    "department_name": result.selectedDepartment.name,
                                    "department_id": result.selectedDepartment.id,
                                    "id":response.group_id,
                                });
                                this.rows = [...this.rows];
                            }
                        },
                        (error) => {
                            console.log('error = ', error);
                        }
                    );
                    this.companiesService.getUserCompanies()
                    .subscribe(
                        (companies) => {
                            console.log('response = ', companies);
                            if (companies){
                                companies.forEach(company => {
                                    if(company.id==this.companyID){
                                        this.companyNameUrl  = company.name;
                                    }
                                });
                            }
                            this.loadingIndicator = false;
                        },
                        (error) => {
                            this.loadingIndicator = false;
                            console.log('error = ', error);
                        }
                    );
                }
                // this.rows.push({
                //     "name": result.groupName,
                //     "department": result.selectedDepartment
                //     })
                // this.rows = [...this.rows];
                
            }
        });
    }

    enableEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            this.editData[rowIndex] = {};
            this.editData[rowIndex]['group_name'] = this.rows[rowIndex]['group_name'];
            this.editData[rowIndex]['department_id'] = this.rows[rowIndex]['department_id'];
            this.editing[rowIndex] = true;
            console.log('this.rows[rowIndex] = ', this.rows[rowIndex]);
            console.log('this.editData[rowIndex] = ', this.editData[rowIndex]);
        }
    }

    cancelEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            delete this.editData[rowIndex];
            this.editing[rowIndex] = false;
        }
    }

    // updateRow(rowIndex) {
    //     console.log("editData = ",this.editData)
    //     console.log('inline editing rowIndex', rowIndex)
    //     if (this.editData[rowIndex]){
    //         this.rows[rowIndex]['name'] = this.editData[rowIndex]['name'];
    //         this.rows[rowIndex]['department'] = this.editData[rowIndex]['department'];
    //         this.rows = [...this.rows];
    //     }
    //     this.cancelEditRow(rowIndex);
    //     this.groupsService.updateGroup(this.rows[rowIndex]);
    //     console.log('UPDATED!', this.rows[rowIndex]['name']);
    //   }

    updateRow(rowIndex) {
     
        if (this.editData[rowIndex]){
            
            const groupID = this.rows[rowIndex]['id'];
            console.log("groupID UP : ",groupID);
            const groupName = this.editData[rowIndex]['group_name'];
            const departmentID = this.editData[rowIndex]['department_id'];
            const department = this.Departments.find(o => o.id === departmentID);
            let departmentName = '';
            if (department){
                departmentName = department.name;
            }
            this.groupsService.updateGroup(groupID, groupName, departmentID)
            .subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response){
                        this.rows[rowIndex]['group_name'] = groupName;
                        this.rows[rowIndex]['department_name'] = departmentName;
                        this.rows[rowIndex]['department_id'] = departmentID;
                        this.rows = [...this.rows];
                        this.cancelEditRow(rowIndex);
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
            
        }

      }
      deleteRow(rowIndex){
        const result = confirm("Want to delete?");
        if (result) {
            this.groupsService.deleteGroup(this.rows[rowIndex]['id']).
            subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response.isSucceeded){
                        this.rows = this.rows.filter(item => item.id !== this.rows[rowIndex]['id']);
                        this.rows = [...this.rows];
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
        }
    }
      
}

@Component({
    selector   : 'add-group-dialog',
    templateUrl: 'add-group-dialog.html'
})
export class DialogOverviewExampleDialog
{

    constructor(
        public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
        @Inject(MAT_DIALOG_DATA) public data: any
    )
    {
    }

    onNoClick(): void
    {
        this.dialogRef.close();
    }

}


