import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class BuildingsService {

  baseURL: string;

  getCompanyBuildings(companyID){
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'read buildings',
        'company_id': companyID
      })
    );
  }

  addBuilding(companyID, name, city, address, zip){
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'add building',
        'company_id': companyID,
        'name': name,
        'city': city,
        'address': address,
        'zip': zip
      })
    );
  }

  updateBuilding(companyID,buildingID, dataBuildingForEdit){
    const url = this.baseURL + '/models/edit.php';
    console.log("dataBuildingorEdit",dataBuildingForEdit);
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'edit building',
        'company_id': companyID,
        'building_id': buildingID,
        'building_name': dataBuildingForEdit.name,
        'building_city': dataBuildingForEdit.city,
        'building_address': dataBuildingForEdit.address,
        'building_zip': dataBuildingForEdit.zip,
      })
    ); 
  } 

  
  deleteBuilding(BuildingID){ 
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'delete building',
        'building_id': BuildingID,
      })
    ); 
  }

  constructor(private httpClient: HttpClient) { 
    this.baseURL =       '//35.231.189.174'; //  '//' + window.location.hostname;
  }

}



