import { Component, OnInit, Inject } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

import { FuseTranslationLoaderService } from '../../../../../core/services/translation-loader.service';
import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import { TeamsService } from '../../../../../main/services/teams-service/teams-service.service';
import { EmployeesService } from '../../../../../main/services/employees-service/employees-service.service';
import { DepartmentsService } from '../../../../../main/services/departments-service/departments-service.service';
import { CompaniesService } from '../../../../../main/services/companies-service/companies-service.service';


@Component({
    selector   : 'admin-employees',
    templateUrl: './admin-employees.component.html',
    styleUrls  : ['./admin-employees.component.scss']
})

export class AdminEmployeesComponent implements OnInit
{

    rows;
    editing = {};
    editData = {};
    loadingIndicator = true;
    reorderable = true;
    Departments;
    companyID;
    Teams;
    companyNameUrl = '';

    emailFormControl = new FormControl('', [
        Validators.required,
        Validators.email
    ]);

    

    

    constructor(private route: ActivatedRoute,
                private translationLoader: FuseTranslationLoaderService, 
                private employeesService: EmployeesService,
                private departmentsService: DepartmentsService,
                private teamsService: TeamsService,
                private companiesService: CompaniesService,
                public dialog: MatDialog )
    {
        this.translationLoader.loadTranslations(english, turkish);
    }

    ngOnInit()
    {
        this.companyID = null;

        this.route.params
        .subscribe(
            (params: Params) => {
                if (params['companyID']) {
                    this.companyID = parseInt(params['companyID'], 10);
                    if (this.companyID) {
                        this.employeesService.getCompanyEmployees(this.companyID)
                            .subscribe(
                                (response) => {
                                    console.log('response getCompanyEmployees = ', response);
                                    if (response) {
                                        this.rows = response;
                                    }
                                    this.loadingIndicator = false;
                                },
                                (error) => {
                                    this.loadingIndicator = false;
                                    console.log('error emp= ', error);
                                });
                        this.teamsService.getCompanyTeams(this.companyID)
                            .subscribe(
                                (response) => {
                                    console.log('response team = ', response);
                                    if (response) {
                                        this.Teams = response;
                                    }
                                    this.loadingIndicator = false;
                                },
                                (error) => {
                                    this.loadingIndicator = false;
                                    console.log('error teams = ', error);
                                }
                            );
                        this.companiesService.getUserCompanies()
                            .subscribe(
                                (companies) => {
                                    console.log('getUserCompanies response = ', companies);
                                    console.log('this.companyID = ', this.companyID);
                                    if (companies) {
                                        companies.forEach(company => {
                                            if (parseInt(company.id, 10) === this.companyID) {
                                                this.companyNameUrl = company.name;
                                            }
                                        });
                                    }
                                    this.loadingIndicator = false;
                                },
                                (error) => {
                                    this.loadingIndicator = false;
                                    console.log('error = ', error);
                                }
                            );
                    }
                }
            }
        );


    }

    enableEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            this.editData[rowIndex] = {};
            this.editData[rowIndex]['firstName'] = this.rows[rowIndex]['firstName'];
            this.editData[rowIndex]['lastName'] = this.rows[rowIndex]['lastName'];
            this.editData[rowIndex]['phone'] = this.rows[rowIndex]['phone'];
            this.editData[rowIndex]['email'] = this.rows[rowIndex]['email'];
            this.editData[rowIndex]['address'] = this.rows[rowIndex]['address'];
            this.editData[rowIndex]['departments'] = this.rows[rowIndex]['departments'];
            this.editData[rowIndex]['team_id'] = this.rows[rowIndex]['team_id'];
            this.editing[rowIndex] = true;
        }
    }

    cancelEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            delete this.editData[rowIndex];
            this.editing[rowIndex] = false;
        }
    }

    // updateRow(rowIndex) {
        
    //     console.log("editData = ",this.editData)
    //     console.log('inline editing rowIndex', rowIndex)
    //     if (this.editData[rowIndex]){
    //         this.rows[rowIndex]['firstName'] = this.editData[rowIndex]['firstName'];
    //         this.rows[rowIndex]['lastName'] = this.editData[rowIndex]['lastName'];
    //         this.rows[rowIndex]['phone'] = this.editData[rowIndex]['phone'];
    //         this.rows[rowIndex]['email'] = this.editData[rowIndex]['email'];
    //         this.rows[rowIndex]['departments'] = this.editData[rowIndex]['departments'];
    //         this.rows = [...this.rows];
    //     }
    //     this.cancelEditRow(rowIndex);
    //     console.log('UPDATED!', this.rows[rowIndex]['name']);
    //   }

    updateRow(rowIndex) {
     
        if (this.editData[rowIndex]){
            
            console.log('this.rows[rowIndex]id] = ', this.rows[rowIndex]['id']);
            console.log('this.editData[rowIndex][name] = ', this.editData[rowIndex] ); // this.editData[rowIndex]['name']
            const teamID = this.editData[rowIndex]['team_id'];
            const team = this.Teams.find(o => o.id === teamID);
            let teamName = '';
            if (team) {
                teamName = team.team_name;
            }
            this.employeesService.updateEmployee(this.companyID, this.rows[rowIndex]['id'], this.editData[rowIndex])
            .subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response){
                        this.rows[rowIndex]['firstName'] = this.editData[rowIndex]['firstName'];
                        this.rows[rowIndex]['lastName'] = this.editData[rowIndex]['lastName'];
                        this.rows[rowIndex]['phone'] = this.editData[rowIndex]['phone'];
                        this.rows[rowIndex]['email'] = this.editData[rowIndex]['email'];
                        this.rows[rowIndex]['address'] = this.editData[rowIndex]['address'];
                        this.rows[rowIndex]['team_name'] = teamName;
                        this.rows[rowIndex]['team_id'] = teamID;
                        this.rows = [...this.rows];
                        this.cancelEditRow(rowIndex);
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
            
        }

      }

      deleteRow(rowIndex){
        const result = confirm("Want to delete?");
        if (result) {
            this.employeesService.deleteEmployee(this.rows[rowIndex]['id']).
            subscribe(
                (response) => {
                    console.log('response = ', response);
                    if ((response as any).isSucceeded){
                        this.rows = this.rows.filter(item => item.id !== this.rows[rowIndex]['id']);
                        this.rows = [...this.rows];
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
        }
    }


    openDialog(): void
    {
        const dialogRef = this.dialog.open(AddEmployeeDialogComponent, {
            width: '250px',
            data : {
                Teams: this.Teams,
            }
        });
        this.companiesService.getUserCompanies()
            .subscribe(
                (companies) => {
                    console.log('response = ', companies);
                    if (companies){
                        companies.forEach(company => {
                            if (parseInt(company.id, 10) === this.companyID){
                                this.companyNameUrl  = company.name;
                            }
                        });
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
        dialogRef.afterClosed().subscribe(result => {
            if (result){
                this.employeesService.addEmployee(this.companyID, result.employeeFirstName,
                    result.employeeLastName, result.employeePhone, result.employeeEmail,
                    result.employeeAddress, result.selectedTeam.id)
                .subscribe(
                    (response) => {
                        console.log('response = ', response);
                        if (response.isSucceeded){
                            this.rows.push({
                                'firstName': result.employeeFirstName,
                                'lastName': result.employeeLastName,
                                'phone': result.employeePhone,
                                'email': result.employeeEmail,
                                'address': result.employeeAddress,
                                'team_name': result.selectedTeam.team_name,
                                'team_id': result.selectedTeam.id,
                                'id': response.employee_id,                              
                                
                                
                            });
                            this.rows = [...this.rows];
                        }
                    },
                    (error) => {
                        console.log('error = ', error);
                    }
                );
                
                
            }
        });
    }

}
@Component({
    selector   : 'add-employee-dialog',
    templateUrl: 'add-employee-dialog.html'
})
export class AddEmployeeDialogComponent
{

    constructor(
        public dialogRef: MatDialogRef<AddEmployeeDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any
    )
    {
    }

    onNoClick(): void
    {
        this.dialogRef.close();
    }

}



