import { Component, Inject } from '@angular/core';
import { FuseTranslationLoaderService } from '../../../../../core/services/translation-loader.service';
import {TeamsService} from '../../../../../main/services/teams-service/teams-service.service';
import { DepartmentsService } from '../../../../../main/services/departments-service/departments-service.service';
import { GroupsService } from '../../../../../main/services/groups-service/groups-service.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';
import { ActivatedRoute } from '@angular/router';
import { CompaniesService } from '../../../../../main/services/companies-service/companies-service.service';

@Component({
    selector   : 'admin-teams',
    templateUrl: './admin-teams.component.html',
    styleUrls  : ['./admin-teams.component.scss']
})
export class AdminTeamsComponent
{
    companyID;
    rows: any[];
    editing = {};
    editData = {}
    loadingIndicator = true;
    reorderable = true;

    teamsService;
    departmentsService;
    groupsService;
    Departments: any[];
    Groups : any[];
    companyNameUrl="";
    constructor(private route: ActivatedRoute,private translationLoader: FuseTranslationLoaderService,
        public tmService:TeamsService,public depService:DepartmentsService,
         public grService:GroupsService,public dialog: MatDialog
         ,private companiesService: CompaniesService)
         
    {
        this.translationLoader.loadTranslations(english, turkish);
        this.teamsService = tmService;
        this.groupsService = grService;
    }

    ngOnInit()
    {
        this.loadingIndicator = false;
        this.companyID = null;
        if (this.route.snapshot.params['companyID']){
            this.companyID = parseInt(this.route.snapshot.params['companyID'], 10);
          }
        if (this.companyID){
            this.teamsService.getCompanyTeams(this.companyID)
            .subscribe(
                (response) => {  
                    console.log('response team = ', response);
                    if (response){
                        this.rows = response;
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error teams = ', error);
                }
            );
            this.groupsService.getCompanyGroups(this.companyID)
            .subscribe(
                (response) => {  
                    console.log('response group = ', response);
                    if (response){
                        this.Groups = response;
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
            this.companiesService.getUserCompanies()
            .subscribe(
                (companies) => {
                    console.log('response = ', companies);
                    if (companies){
                        companies.forEach(company => {
                            if(company.id==this.companyID){
                                this.companyNameUrl  = company.name;
                            }
                        });
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );
        }

    }

    enableEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            this.editData[rowIndex] = {};
            this.editData[rowIndex]['team_name'] = this.rows[rowIndex]['team_name'];
            this.editData[rowIndex]['group_id'] = this.rows[rowIndex]['group_id'];
            this.editData[rowIndex]['group_name'] = this.rows[rowIndex]['group_name'];
            this.editing[rowIndex] = true;
            console.log('this.rows[rowIndex] = ', this.rows[rowIndex]);
            console.log('this.editData[rowIndex][name] = ', this.editData[rowIndex]); // this.editData[rowIndex]['name']
        }
    }

    cancelEditRow(rowIndex){
        if ((rowIndex !== null) && (typeof rowIndex !== 'undefined')){
            delete this.editData[rowIndex];
            this.editing[rowIndex] = false;
        }
    }

    updateRow(rowIndex) {
     
        if (this.editData[rowIndex]){
            
            console.log('this.rows[rowIndex]ID] = ', this.rows[rowIndex]['id']);
            console.log('this.editData[rowIndex][name] = ', this.editData[rowIndex] ); // this.editData[rowIndex]['name']
            // teamID, teamName, groupID
            const teamID = this.rows[rowIndex]['id'];
            const teamName = this.editData[rowIndex]['team_name'];
            const groupID = this.editData[rowIndex]['group_id'];
            const group = this.Groups.find(o => o.id === groupID);
            let groupName = '';
            if (group) {
                groupName = group.group_name;
            }


            this.teamsService.updateTeam(teamID, teamName, groupID)
            .subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response){
                        this.rows[rowIndex]['team_name'] = teamName;
                        this.rows[rowIndex]['group_name'] = groupName;
                        this.rows[rowIndex]['group_id'] = groupID;
                        this.rows = [...this.rows];
                        this.cancelEditRow(rowIndex);
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
            
        }

      }

    // updateRow(rowIndex) {
    //     console.log("editData = ",this.editData)
    //     console.log('inline editing rowIndex', rowIndex)
    //     if (this.editData[rowIndex]){
    //         this.rows[rowIndex]['name'] = this.editData[rowIndex]['name'];
    //         //this.rows[rowIndex]['department'] = this.editData[rowIndex]['department'];
    //         this.rows[rowIndex]['group'] = this.editData[rowIndex]['group'];
    //         this.rows = [...this.rows];
    //     }
    //     this.cancelEditRow(rowIndex);
    //     this.teamsService.updateTeam(this.rows[rowIndex]);
    //     console.log('UPDATED!', this.rows[rowIndex]['name']);
    //   }
    
    deleteRow(rowIndex){
        const result = confirm("Want to delete?");
        if (result) {
            this.teamsService.deleteTeam(this.rows[rowIndex]['id']).
            subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response.isSucceeded){
                        this.rows = this.rows.filter(item => item.id !== this.rows[rowIndex]['id']);
                        this.rows = [...this.rows];
                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
            );
        }
    }
      

    openDialog(): void
    {
        let dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
            width: '250px',
            data : {
                Groups:this.Groups,
            }
        });

        dialogRef.afterClosed().subscribe(result => {
            if (result){
                console.log("result: ",result)
                console.log("result this.companyID: ",this.companyID);
                console.log("result result.teamName: ",result.teamName);
                console.log("result result.selectedGroup.id: ",result.selectedGroup.id);
                console.log("result result.selectedGroup.group_name: ",result.selectedGroup.group_name);
                this.teamsService.addTeam(this.companyID,result.teamName,result.selectedGroup.id).subscribe(
                    (response) => {
                        console.log('response = ', response);
                        if (response.isSucceeded){
                            this.rows.push({
                                "team_name": result.teamName,
                                "group_name":result.selectedGroup.group_name,
                                "group_id":result.selectedGroup.id,
                                "id":response.team_id
                            });
                            this.rows = [...this.rows];
                        }
                    },
                    (error) => {
                        console.log('error = ', error);
                    }
                );
                
                
            }
        });
    }

   
}
@Component({
    selector   : 'add-team-dialog',
    templateUrl: 'add-team-dialog.html'
})
export class DialogOverviewExampleDialog
{

    constructor(
        public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
        @Inject(MAT_DIALOG_DATA) public data: any
    )
    {
    }

    onNoClick(): void
    {
        this.dialogRef.close();
    }

}



