import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { AuthService } from '../auth-service/auth.service';

@Injectable()
export class CompaniesService {

  baseURL: string;


  getUserCompanies(){
    const url = this.baseURL + '/controllers/company.php';
    const token = this.authService.getToken();

    return this.httpClient.get<Array<any>>(url,
      {
        params: new HttpParams().set('action', 'read companies').set('token', token)
      }
    );
  }

  getCompanyName(){
    
  }

  addCompany(companyName){
      const url = this.baseURL + '/controllers/company.php';
      const token = this.authService.getToken();
      const formData: FormData = new FormData();
      formData.append('token', token);
      formData.append('action', 'add company');
      formData.append('name', companyName);

      return this.httpClient.post<any>(url, formData);
  }

  deleteCompany(companyID){
    // this.companies = this.companies.filter(item => item.ID !== companyID);
  }

  constructor(private httpClient: HttpClient,
              private authService: AuthService) { 
                this.baseURL =       '//35.231.189.174'; //  '//' + window.location.hostname;
              }

}



