import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { AuthService } from '../auth-service/auth.service';

@Injectable()
export class UserService {

  baseURL: string;

  getUserDetails() {
    const token = this.authService.getToken();

    const url = this.baseURL + '/controllers/user.php';
    return this.httpClient.get<UserDetailsResponse>(url,
      {
        params: new HttpParams().set('action', 'get user details').set('token', token)
      }
    );
  }

  constructor(private httpClient: HttpClient,
              private authService: AuthService) {
      this.baseURL =       '//35.231.189.174'; //  '//' + window.location.hostname;
   }

   

}

interface UserDetailsResponse {
  firstName: string;
  lastName: string;
}




