import { Component , NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { LeafletModule } from '@asymmetrik/ngx-leaflet';
import { LeafletDrawModule } from '@asymmetrik/ngx-leaflet-draw';
import { AgmCoreModule, GoogleMapsAPIWrapper } from '@agm/core';
import 'hammerjs';
import { SharedModule } from './core/modules/shared.module';
import { AppComponent } from './app.component';
import { FuseMainModule } from './main/main.module';
import { FuseSplashScreenService } from './core/services/splash-screen.service';
import { FuseConfigService } from './core/services/config.service';
import { FuseNavigationService } from './core/components/navigation/navigation.service';
import { AuthService } from './main/services/auth-service/auth.service';
import { AuthGuard } from './main/services/auth-guard-service/auth-guard.service';
import { UserService } from './main/services/user-service/user.service';
import { DepartmentsService } from './main/services/departments-service/departments-service.service';
import { GroupsService } from './main/services/groups-service/groups-service.service';
import { TeamsService } from './main/services/teams-service/teams-service.service';
import { EmployeesService } from './main/services/employees-service/employees-service.service';
import { BuildingsService } from './main/services/buildings-service/buildings-service.service';
import { CompaniesService } from './main/services/companies-service/companies-service.service';
import { PlaceEmployeeService } from './main/services/place-employee-service/place-employee.service';
import { FloorsService } from './main/services/floors-service/floors-service.service';
import { RoomsService } from './main/services/rooms-service/rooms-service.service';
import { FuseSampleModule } from './main/content/sample/sample.module';
import { LoginModule } from './main/content/auth/login/login.module';
import { ForgotPasswordModule } from './main/content/auth/forgot-password/forgot-password.module';
import { AddCompanyModule } from './main/content/admin/organization/admin-companies/add-company/add-company.module';
import { AdminDepartmentsModule } from './main/content/admin/organization/admin-departments/admin-departments.module';
import { AdminGroupsModule } from './main/content/admin/organization/admin-groups/admin-groups.module';
import { AdminTeamsModule } from './main/content/admin/organization/admin-teams/admin-teams.module';
import { AdminEmployeesModule } from './main/content/admin/organization/admin-employees/admin-employees.module';
import { AdminBuildingsModule } from './main/content/admin/architecture/admin-buildings/admin-buildings.module';
import { AdminFloorsModule } from './main/content/admin/architecture/admin-floors/floors/admin-floors.module';
import { AddFloorModule } from './main/content/admin/architecture/admin-floors/add-floor/add-floor.module';
import { PlaceEmployeeModule } from './main/content/floor-plan/place-employee/place-employee.module';
import { EditFloorModule } from './main/content/admin/architecture/admin-floors/edit-floor/edit-floor.module';
import { FloorMapModule } from './main/content/components/floor-map/floor-map.module';
// import { ChairPopup } from './main/content/components/floor-map/floor-map.component';
import { TranslateModule } from '@ngx-translate/core';

const appRoutes: Routes = [
    {
        path      : '**',
        redirectTo: 'sample'
    }/*,
    {
        path      : 'admin/edit/floor/:id',
        component: EditFloorComponent
    }*/
];

@NgModule({
    declarations: [
        AppComponent   
    ],
    imports     : [
        BrowserModule,
        HttpModule,
        HttpClientModule,
        BrowserAnimationsModule,
        RouterModule.forRoot(appRoutes),
        LeafletModule.forRoot(),
        LeafletDrawModule.forRoot(),
        SharedModule,
        TranslateModule.forRoot(),
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyCRozu0G6ZU2eZtpj20AheDFk-_ewFW8Ps'
          }),
        FuseMainModule,
        LoginModule,
        ForgotPasswordModule,
        FuseSampleModule,
        AddCompanyModule,
        AdminDepartmentsModule,
        AdminGroupsModule,
        AdminTeamsModule,
        AdminEmployeesModule,
        AdminBuildingsModule,
        AdminFloorsModule,
        AddFloorModule,
        EditFloorModule,
        PlaceEmployeeModule,
        FloorMapModule
    ],
    providers   : [
        FuseSplashScreenService,
        FuseConfigService,
        FuseNavigationService,
        GoogleMapsAPIWrapper,
        AuthService,
        AuthGuard,
        UserService,
        DepartmentsService,
        GroupsService,
        TeamsService,
        EmployeesService,
        BuildingsService,
        FloorsService,
        RoomsService,
        CompaniesService,
        PlaceEmployeeService
    ],
    entryComponents: [],
    bootstrap   : [
        AppComponent
    ]
})
export class AppModule
{
}



