import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { SharedModule } from '../../../../../core/modules/shared.module';

import { AdminEmployeesComponent, AddEmployeeDialogComponent } from './admin-employees.component';

import { AuthGuard } from '../../../../services/auth-guard-service/auth-guard.service';

const routes = [
    {
        path: 'admin/company/:companyID/employees',
        component: AdminEmployeesComponent,
        canActivate: [AuthGuard]
    }
];

@NgModule({
    declarations: [
        AdminEmployeesComponent,
        AddEmployeeDialogComponent
    ],
    entryComponents: [AddEmployeeDialogComponent],
    imports     : [
        SharedModule,
        RouterModule.forChild(routes)
    ],
    exports     : [
        AdminEmployeesComponent
    ]
})

export class AdminEmployeesModule
{
}



