import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class GroupsService {
    baseURL: string;
   
  getCompanyGroups(companyID){
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url, 
        JSON.stringify({
        'controller': 'read groups',
        'company_id': companyID
      })
    );
  }


addGroup(companyID, groupName, departmentID){
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'add group',
        'name': groupName,
        'company_id': companyID,
        'department_id': departmentID
      })
    );
  }

//    deleteGroup(groupsID){
// //     this.groups = this.groups.filter(item => item.ID !== groupsID);
//    }


  updateGroup(groupID, groupName, departmentID ) {
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'edit group',
        'group_id': groupID,
        'group_name': groupName,
        'department_id': departmentID
      })
    );
  } 



  findIndexToUpdate(dataDepartmentForEdit) { 
        return dataDepartmentForEdit.ID === this;
  }

  deleteGroup(groupID){ 
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'delete group',
        'group_id': groupID,
      })
    ); 
  }

  constructor(private httpClient: HttpClient) { 
    this.baseURL =       '//35.231.189.174'; //  '//' + window.location.hostname;
  }

}



