import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable()
export class TeamsService {
  baseURL: string;
 
  getCompanyTeams(companyID){
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url, 
        JSON.stringify({
        'controller': 'read teams',
        'company_id': companyID
      })
    );
  }



  addTeam(companyID, teamName, groupID){
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'add team',
        'name': teamName,
        'company_id': companyID,
        'group_id': groupID
      })
    );
  }

  updateTeam(teamID, teamName, groupID){
    const url = this.baseURL + '/models/edit.php';
    // console.log("dataTeamForEdit",dataTeamForEdit);
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'edit team',
        'team_id': teamID,
        'team_name': teamName,
        'group_id': groupID
      })
    );
  } 


  findIndexToUpdate(dataTeamForEdit) { 
    return dataTeamForEdit.ID === this;
}

  // deleteTeam(teamID){
  //  // this.teams = this.teams.filter(item => item.ID !== teamID);
  // }

  deleteTeam(teamID){ 
    const url = this.baseURL + '/models/edit.php';
    return this.httpClient.post(url,
      JSON.stringify({
        'controller': 'delete team',
        'team_id': teamID,
      })
    ); 
  }
  
  constructor(private httpClient: HttpClient) { 
    this.baseURL =       '//35.231.189.174'; //  '//' + window.location.hostname;
  }

}



