import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { SharedModule } from '../../../../../core/modules/shared.module';

import { AdminBuildingsComponent, AddBuildingDialog } from './admin-buildings.component';

import { AuthGuard } from '../../../../services/auth-guard-service/auth-guard.service';

const routes = [
    {
        path: 'admin/company/:companyID/buildings',
        component: AdminBuildingsComponent,
        canActivate: [AuthGuard]
    }
];

@NgModule({
    declarations: [
        AdminBuildingsComponent,
        AddBuildingDialog
    ],
    entryComponents: [AddBuildingDialog],
    imports     : [
        SharedModule,
        RouterModule.forChild(routes)
    ],
    exports     : [
        AdminBuildingsComponent
    ]
})

export class AdminBuildingsModule
{
}



