import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { LeafletModule } from '@asymmetrik/ngx-leaflet';
import { LeafletDrawModule } from '@asymmetrik/ngx-leaflet-draw';

import { SharedModule } from '../../../../core/modules/shared.module';

import { FloorMapComponent, AddRoomNumberDialogComponent} from './floor-map.component';



@NgModule({
    declarations: [
        FloorMapComponent,
        AddRoomNumberDialogComponent
    ],
    entryComponents: [AddRoomNumberDialogComponent],
    imports     : [
        SharedModule,
        LeafletModule,
        LeafletDrawModule
    ],
    exports     : [
        FloorMapComponent
    ]
})

export class FloorMapModule
{
}



