import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { FuseTranslationLoaderService } from '../../../../../../core/services/translation-loader.service';
import { FuseNavigationService } from '../../../../../../core/components/navigation/navigation.service';

import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';

import { CompaniesService } from '../../../../../../main/services/companies-service/companies-service.service';

@Component({
    selector   : 'add-company',
    templateUrl: './add-company.component.html',
    styleUrls: ['./add-company.component.scss']
})
export class AddCompanyComponent
{
    constructor(private translationLoader: FuseTranslationLoaderService, 
                private fuseNavigationService: FuseNavigationService,
                private companiesService: CompaniesService,
                private router: Router)
    {
        this.translationLoader.loadTranslations(english, turkish);
    }

    addCompany(companyName){
        console.log('addCompany. companyName = ' + companyName);
        if (companyName){
            this.companiesService.addCompany(companyName).subscribe(
                (response) => {
                    console.log('addCompany. response = ', response);
                    if (response && response.isSucceeded && response.companyID) {
                        const companyID = response.companyID;
                        const navModel = this.fuseNavigationService.getNavigationModel();
                        console.log('navModel = ', navModel);
                        const adminPanel = navModel.find(o => o.id === 'admin-panel');
                        console.log('adminPanel = ', adminPanel);
                        if (adminPanel){
                            adminPanel.children.push(
                                {
                                    'id': 'company' + companyID,
                                    'title': companyName,
                                    'type': 'collapse',
                                    'icon': 'border_all',
                                    'children': [
                                        {
                                            'id': 'company' + companyID + 'Organization',
                                            'title': 'Organization',
                                            'type': 'collapse',
                                            'icon': 'group',
                                            'children': [
                                                {
                                                    'id': 'company' + companyID + 'Departments',
                                                    'title': 'Departments',
                                                    'type': 'item',
                                                    'url': '/admin/company/' + companyID + '/departments'
                                                },
                                                {
                                                    'id': 'company' + companyID + 'Groups',
                                                    'title': 'Groups',
                                                    'type': 'item',
                                                    'url': '/admin/company/' + companyID + '/groups'
                                                },
                                                {
                                                    'id': 'company' + companyID + 'Teams',
                                                    'title': 'Teams',
                                                    'type': 'item',
                                                    'url': '/admin/company/' + companyID + '/teams'
                                                },
                                                {
                                                    'id': 'company' + companyID + 'Employees',
                                                    'title': 'Employees',
                                                    'type': 'item',
                                                    'url': '/admin/company/' + companyID + '/employees'
                                                }
                                            ]
                                        },
                                        {
                                            'id': 'company' + companyID + 'Architecture',
                                            'title': 'Architecture',
                                            'type': 'collapse',
                                            'icon': 'location_city',
                                            'children': [
                                                {
                                                    'id': 'company' + companyID + 'Buildings',
                                                    'title': 'Buildings',
                                                    'type': 'item',
                                                    'url': '/admin/company/' + companyID + '/buildings'
                                                },
                                                {
                                                    'id': 'company' + companyID + 'Floors',
                                                    'title': 'Floors',
                                                    'type': 'item',
                                                    'url': '/admin/company/' + companyID + '/floors'
                                                }
                                            ]
                                        }
                                    ]
                                }
                            );
                            
                            // Navigate to departments page of the new company
                            this.router.navigate(['/admin/company/' + companyID + '/departments']);

                        }
                    }
                },
                (error) => {
                }
            );
        }

    }
}



