import { Component, Inject, ViewEncapsulation, Input, Output, EventEmitter, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';

import { geoJSON, Layer, latLng, tileLayer, imageOverlay, circle, polygon, rectangle, CRS, LatLngBounds, Map, marker, icon, layerGroup } from 'leaflet';
import { FuseTranslationLoaderService } from '../../../../core/services/translation-loader.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FloorsService } from '../../../../main/services/floors-service/floors-service.service';
import { EmployeesService } from '../../../../main/services/employees-service/employees-service.service';
import { PlaceEmployeeService } from '../../../../main/services/place-employee-service/place-employee.service';
import { RoomsService } from '../../../../main/services/rooms-service/rooms-service.service';

import { locale as english } from './i18n/en';
import { locale as turkish } from './i18n/tr';

@Component({
    // tslint:disable-next-line:component-selector
    selector   : 'floor-map',
    templateUrl: './floor-map.component.html',
    styleUrls  : ['./floor-map.component.scss'],
})
export class FloorMapComponent implements OnInit, OnDestroy
{
    Rooms: any[];
    roomsService;
    loadingIndicator = true;
    employeePlacedSubscription: Subscription;
    chairEmptySubscription: Subscription;

    private _chairs: Array<{ID: number, latitude: number, longitude: number , placedEmployeeID: number}>;
    isMapReady = false;
    freeChairOptions    = {
        radius: 2.5,
        fillColor: '#5E8C29', 
        color: '#5E8C29', 
        strokeWidth: '3',
        className: ''
    };
    placedChairOptions     = {
        radius: 2.5,
        fillColor: '#ff0', 
        color: '#0af', 
        strokeWidth: '3',
        className: ''
    };
    
    private _floorID: number;
    // floor: FloorData;
    floor;
    imageLayer;

    companyID;
    companyEmployees;


    constructor(private floorsService: FloorsService, 
        private placeEmployeeService: PlaceEmployeeService,
        private employeesService: EmployeesService,
        public dialog: MatDialog,
        public rmService: RoomsService) {
            this.roomsService = rmService;

            
    }



    @Input()
    set floorID(id: number) {

        this._floorID = id;

        this.floorsService.getFloor(this._floorID)
            .subscribe(
                (response) => {
                    console.log('response = ', response);
                    if (response) {
                        this.floor = response;
                        console.log('this.floor.img = ', this.floor.img);
                        if (this.floor && (this.companyID !== this.floor.companyID)) {
                            console.log('IF sentance');
                            this.companyID = this.floor.companyID;
                            this.updateEmployees(err => {
                                if (this.isMapReady) {
                                    const url = '//' + window.location.hostname + this.floor.img;
                                    this.changeImageURL(url);
                                    this.updateChairsGroup();
                                    this.addEmployeeDragEvents();
                                }
                            });
                            // this.companyEmployees = this.employeesService.getCompanyEmployees(this.companyID);
                            // console.log('this.companyEmployees = %o', this.companyEmployees);
                        } else {
                            if (this.isMapReady) {
                                const url = '//' + window.location.hostname + this.floor.img;
                                this.changeImageURL(url);
                                this.updateChairsGroup();
                                this.addEmployeeDragEvents();
                            }
                        }

                    }
                },
                (error) => {
                    console.log('error = ', error);
                }
        );
        // this.floor =  this.floorsService.getFloor(id);

        // console.log('set floor id. floor = %o', this.floor);


    }


    @Input() enableAddChairs: boolean;
    @Input() enableAddAreas: boolean;

    



    chairsLayerGroup =  layerGroup();

    roomsLayerGroup =  layerGroup(
        // polygon([[ -79, 89 ], [ -82, 144 ], [ -109.5, 90.5 ], [-108.5, 145.5 ]])
        // new rectangle([[-83.5, 90], [-108.5, 144.5]], {color: "#ff7800", weight: 1})
    );



layers = [
    this.roomsLayerGroup,
    this.chairsLayerGroup

];

    imageBounds = [[0, 0], [1000, 1000]];
    options = {
        layers: [
        ],
        zoom: 1,
        minZoom: 1,
        maxZoom: 3,
        // center: latLng(46.879966, -121.726909),
        center: latLng(0, 0),
        attributionControl: false,
        scrollWheelZoom: true,
        crs: CRS.Simple,
        zoomControl: false,
        dragging: true
        // maxBounds: this.imageBounds
    };

    layersControl = {
        overlays: {
            'Chairs': this.chairsLayerGroup,
            // 'Big Circle': circle([ -150, 150 ], { radius: 50 }),
            'Rooms': this.roomsLayerGroup
        }
    };

    drawOptions = {
        position: 'topright',
        draw: {
            marker: false,
            polyline: false,
            circle: {
                shapeOptions: {
                    color: '#aaaaaa'
                }
            }
        }
    };

    ngOnInit() {

        // Employee placed event
        this.employeePlacedSubscription = this.placeEmployeeService.employeePlaced
            .subscribe(
                (event: { employeeID: number, chairID: number }) => {
                    const employeeID = event.employeeID;
                    const chairID = event.chairID;
                    console.log('Floor Map. employeePlaced. employeeID= %o, chairID=%o', employeeID, chairID);
                    console.log('this.floor.chairs = ', this.floor.chairs);
                    const obj = this.floor.chairs.find(o => o.id === chairID);
                    console.log('obj = ', obj);
                    if (obj) {
                        obj.employeeID = employeeID;
                        this.updateChairsGroup();
                        this.addEmployeeDragEvents();
                    }

                }
            );
        
        // Chair Empty event
        this.chairEmptySubscription = this.placeEmployeeService.chairEmpty
            .subscribe(
                (chairID: number ) => {
                    console.log('Floor Map. employeeUnplaced.  chairID=%o', chairID);
                    console.log('this.floor.chairs = ', this.floor.chairs);
                    const obj = this.floor.chairs.find(o => o.id === chairID);
                    console.log('obj = ', obj);
                    if (obj) {
                        obj.employeeID = null;
                        this.updateChairsGroup();
                        this.addEmployeeDragEvents();
                    }


                }
            );
            this.loadingIndicator = false;
            this.roomsService.getRooms(this._floorID)
            .subscribe(
                (response) => {  
                    console.log('response get rooms = ', response);
                    if (response){
                        this.Rooms = response;
                    }
                    this.loadingIndicator = false;
                },
                (error) => {
                    this.loadingIndicator = false;
                    console.log('error = ', error);
                }
            );

    }

    updateChairsGroup(){
        console.log('updateChairsGroup. this.floor =', this.floor);
        console.log('updateChairsGroup. this.floor.chairs =', this.floor.chairs);
        this.chairsLayerGroup.clearLayers();
        if (this.floor && this.floor.chairs){
            this.floor.chairs.forEach(chair => {
                // console.log('chair = %o', chair);
                let chairClass = '';
                if (chair.id){
                    chairClass = 'place-' + chair.id.toString();
                }
                let options: object;
                if (chair.employeeID){
                    options = Object.assign({}, this.placedChairOptions);
                } else {
                    options = Object.assign({}, this.freeChairOptions);
                }
                options['className'] = chairClass;
                const layer = circle([chair.lat, chair.lng], options);
                
                if (chair.employeeID && this.companyEmployees) {
                    console.log('chair.employeeID = ', chair.employeeID);
                    console.log('this.companyEmployees = ', this.companyEmployees);
                    const employee = this.companyEmployees.find(o => o.id === chair.employeeID);
                    if (employee && employee.firstName && employee.lastName){
                        const employeeName = employee.firstName + ' ' + employee.lastName;
                        layer.bindTooltip('<h2>' + employeeName + ' </h2>');
                    }
                } else {
                    layer.bindTooltip('<h2>This place is free.</h2>');
                }
                this.chairsLayerGroup.addLayer(layer);
            });
        }  
    }

    initFloorChairs(){
        if (this.floor && this.floor.chairs){
            this.updateChairsGroup();
        }
        this.chairsLayerGroup.on('add', evt => this.addEmployeeDragEvents());
    }

    onMapClick(e){
        
        const lat = e.latlng.lat;
        const lng = e.latlng.lng;
        console.log('onMapClick.lat=%o,lng=%o', lat, lng);
        if (this.enableAddChairs){
            const dialogRef = this.dialog.open(AddRoomNumberDialogComponent, {
                width: '250px',
                data : {
                    Rooms: this.Rooms
                }
            });
    
            dialogRef.afterClosed().subscribe(result => {
                if (result){
                    this.floorsService.addChair(this._floorID, lat, lng, result.selectedRoom.id)
                    .subscribe(
                        (response) => {
                            console.log('response = ', response);
                            if (response) {
                                this.chairsLayerGroup.addLayer(circle([lat, lng], { radius: 2.5, fillColor: '#5E8C29', color: '#5E8C29' }));
                            }
                        },
                        (error) => {
                            console.log('error = ', error);
                        }
                    );  
                }
            });
            // this.floorsService.addChair(this._floorID, lat, lng, null);
            // this.chairsLayerGroup.addLayer(circle([ lat, lng ], { radius: 2.5, fillColor: '#5E8C29', color: '#5E8C29' }));
            // this.floorsService.addChair(this._floorID, lat, lng, null)
            //     .subscribe(
            //         (response) => {
            //             console.log('response = ', response);
            //             if (response) {
            //                 this.chairsLayerGroup.addLayer(circle([lat, lng], { radius: 2.5, fillColor: '#5E8C29', color: '#5E8C29' }));
            //             }
            //         },
            //         (error) => {
            //             console.log('error = ', error);
            //         }
            //     );  
        }    
    }

    // openDialog(): void
    // {
    //     let dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
    //         width: '250px',
    //         data : {
    //         }
    //     });

    //     dialogRef.afterClosed().subscribe(result => {
    //         if (result){
                
    //         }
    //     });

        
    // }

    addEmployeeDragEvents(){
        // console.log('onMapLoaded');
        const chairs = Array.from(document.querySelectorAll('path.leaflet-interactive'));
        
        // console.log('chairs = %o', chairs);
        if (chairs){
            chairs.forEach((item) => {
                // console.log('chair item = %o', item);
                item.addEventListener('dragenter', function (e: DragEvent ) {
                    if (e.target) {
                        let targetElement: any;
                        targetElement = e.target;
                       targetElement.classList.add('employee-drag-over');
                    }
                    // console.log('dragenter!. e=%o, e.target=', e, e.target);
                });
                item.addEventListener('dragleave', function (e: DragEvent ) {
                    if (e.target) {
                       let targetElement: any;
                       targetElement = e.target;
                       targetElement.classList.remove('employee-drag-over');
                    }
                    // console.log('dragleave! e=', e);
                });
                item.addEventListener('dragover', function (e: DragEvent ) {
                    e.preventDefault();
                    // console.log('dragover! e=', e);
                });
                item.addEventListener('drop', e => {
                    if (e.target) {
                        let targetElement: any;
                        targetElement = e.target;
                        targetElement.classList.remove('employee-drag-over');
                        let chairID = targetElement.className.baseVal.split(' ').find(str => str.includes('place')).split('-')[1];
                        chairID = parseInt(chairID, 10);   
                        this.placeEmployeeService.employeeDroped.emit(chairID);   
                     }
                });
            });
        }

        
    }

    changeImageURL(url){
        console.log('change img url to: ' + url);
        this.imageLayer.setUrl(url);
    }

    onMapReady(map: Map) {
        
        // console.log('onMapReady. map=', map);
        
        // let url = "http://18.195.126.0/img/floors/floor_65.jpg";
        // if (this._floorData.imgURL){
           // let url = this._floorData.imgURL;
        let url = 'http://35.231.189.174/img/loader.gif';
        if (this.floor) {
            url = '//' + window.location.hostname + this.floor.img; 
        } 
        console.log('onMapReady . url = ', url);
        const w = 2882;
        const h = 2367;
        const southWest = map.unproject([0, h], map.getMaxZoom() - 0);
        const northEast = map.unproject([w, 0], map.getMaxZoom() - 0);
        const bounds = new LatLngBounds(southWest, northEast);
        this.imageLayer = imageOverlay(url, bounds);
        this.imageLayer.addTo(map);
        this.isMapReady = true;
        map.on('click', evt => this.onMapClick(evt));
        map.setMaxBounds(bounds);

        if (this.floor) {
            this.updateEmployees(err => {
                this.initFloorChairs();
            });
        } 

    }

    // Update employees according to selected company
    updateEmployees(next) {
        if (this.companyID) {
            this.employeesService.getCompanyEmployees(this.companyID)
                .subscribe(
                    (employees) => {
                        this.companyEmployees = employees;
                        next();
                    },
                    (error) => {
                        console.log('updateEmployees. error = ', error);
                        next(error);
                    });
        }
    }

    ngOnDestroy() {
        this.employeePlacedSubscription.unsubscribe();
        this.chairEmptySubscription.unsubscribe();
    }


}
@Component({
    // tslint:disable-next-line:component-selector
    selector   : 'add-room-number-dialog',
    templateUrl: 'add-room-number-dialog.html'
})
export class AddRoomNumberDialogComponent
{

    constructor(
        public dialogRef: MatDialogRef<AddRoomNumberDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any
    )
    {
    }

    onNoClick(): void
    {
        this.dialogRef.close();
    }

}





